export function ExpoAvApp() {
  const [sound, setSound] = React.useState();
  const uri = "https://playertest.longtailvideo.com/adaptive/alt-audio-no-video/sintel/playlist.m3u8"
  
  async function playSound() {
    console.log('Loading Sound');
    const { sound } = await Audio.Sound.createAsync({
      uri: uri
    });
    setSound(sound);

    console.log('Playing Sound');
   
    /*await Audio.setAudioModeAsync({
      allowsRecordingIOS: false,
      staysActiveInBackground: true, // not work for ios in expo-go, only work in standalone app.
      playsInSilentModeIOS: true,
      interruptionModeIOS: Audio.INTERRUPTION_MODE_IOS_DO_NOT_MIX,

      shouldDuckAndroid: true,
      interruptionModeAndroid: Audio.INTERRUPTION_MODE_ANDROID_DO_NOT_MIX,
      playThroughEarpieceAndroid: false
    })*/

    await sound.playAsync();
  }

  React.useEffect(() => {
    return sound
      ? () => {
          console.log('Unloading Sound');
          sound.unloadAsync();
        }
      : undefined;
  }, [sound]);

  return (
    <View style={styles.container}>

      {/** use hls player for mobile */}
      {PlatformUtils.isWeb && <ReactHlsPlayer
        src={uri}
        autoPlay={true}
        controls={true}
        width="100%"
        height="auto"
      />}
      {/** use av for mobile */}
      {!PlatformUtils.isWeb && 
      <Button  onPress={playSound}>Play Sound </Button>
      }
    </View>
  );
}

const voiceIds = {
  DEFAULT: 'DEFAULT',
  FEMALE: 'FEMALE',
  MALE: 'MALE',
  FEMALE_VIP: 'FEMALE_VIP',
  MALE_VIP: 'MALE_VIP',
}

function TestScreenHLS() {
  const [data, setData] = useState(null);

  const init = async () => {
    // Set up the player
    console.log('Set up the player')
    await TrackPlayer.setupPlayer();
    console.log('Set up the player -> Done')
  


  };

  const start = async () => {
        // Add a track to the queue
      await TrackPlayer.add({
          id: 'trackId',
          url: 'http://localhost:1337/uploads/1-1/index.m3u8',
          title: 'Track Title',
          artist: 'Track Artist',
          artwork: '',
        });

    // Start playing it
    await TrackPlayer.play();
  };

  return (
    <>
     <Button onPress={()=> {
      init();
     }}>

        Init 

      </Button>
      <Button onPress={()=> {
      start();
     }}>

        Start 
      </Button>

      <Button onPress={() => {
        fetch(`http://localhost:1337/api/synther/1/1?voiceId=${voiceIds.DEFAULT}`)
          .then(ret => ret.json())
          .then(data => {
            console.log('got url: ', data);
            const records = data.records;
            const url = 'http://localhost:1337/uploads/' + records[0].filename;
            console.log("URL => ", url);

            setData({url})
          });

      }}>Female</Button>

      <Button onPress={() => {
        fetch(`http://localhost:1337/api/synther/1/1?voiceId=${voiceIds.MALE_VIP}`)
          .then(ret => ret.json())
          .then(data => {
            console.log('got url: ', data);
            setData(data)
          });

      }}>Male Vip</Button>

      {data && data.url && <ReactHlsPlayer
        src={data.url}
        autoPlay={true}
        controls={true}
        width="100%"
        height="auto"
      />}
    </>
  )
}


