
import * as React from 'react';
import { useRef, useEffect } from 'react';

import { View,  StyleSheet } from 'react-native';
import {
    Text,
    Button,
} from 'react-native-paper';

import { usePlayback } from '../../hooks/use-playback';
import { usePlayer } from '../../contexs/player';

import { SYS_SOUD_URIs, HLS_URIs } from '../../constants/player';
const sampleArwork = "https://picsum.photos/200";

const hlsTracks = HLS_URIs.map((url, i) => ({
    title: `track no: ${i}`,
    artist: `M-Audio`,
    album: `HLS DEMO`,
    artwork: [ {  src: sampleArwork, },],
    url: url,
    hls: true,
    isLoaded: true,
}));

const mp3Tracks = SYS_SOUD_URIs.map((url, i) => ({
    title: `track no: ${i}`,
    artist: `M-Audio`,
    album: `Mp3 DEMO`,
    artwork: [ {  src: sampleArwork, },],
    url: url,
    hls: false,
    isLoaded: true,
}));

const SoundUIPlayer = () => {
    console.log("build version X");
    const {play, pause, skipNext, setVolume, setMute, stat, err } = usePlayback();
    const [_, dispatch] = usePlayer();

    const setTracks = (hls)=> {
        dispatch({
            type: 'setTracks',
            payload: hls ? hlsTracks: mp3Tracks,
        })
    };
    return (
        <View>
            <Button onPress={() => setTracks(true)}>set HLS Tracks</Button>
            <Button onPress={() => setTracks()}>set Mp3Tracks</Button>
            <Button onPress={() => play()}>Play</Button>
            <Button onPress={() => pause()} >Pause</Button>
            <Button onPress={() => skipNext()}>Next</Button>
            <Button onPress={() => skipNext()}>Next</Button>
            <Button onPress={() => setVolume(0.5)}>Volume-</Button>
            <Button onPress={() => setVolume(1.0)}>Volume+</Button>
            <Button onPress={() => setMute(true)}>Mute</Button>
            <Button onPress={() => setMute(false)}>UnMute</Button>
            {err &&  <Text>{JSON.stringify(err)}</Text>}
            {stat &&  <Text>{JSON.stringify(stat, null, 2)}</Text>}
           
        </View>
    )
}

const url = "https://pysync.github.io/maudi-web/static/media/test-hls-1/index.m3u8";

const AudioUIOnly = () => {

    const videoRef = useRef(null);

    useEffect(() => {
        if (!videoRef.current) {
            videoRef.current = new window.Audio();
        }

        if (videoRef.current) {

            videoRef.current.oncanplay = ()=> {
                alert("now can play.");
                videoRef.current.play();
            }
        }
    }, [videoRef.current]);

    return (
        <View>
            <Button onPress={()=> {
                if (videoRef.current.canPlayType('application/vnd.apple.mpegurl')) {
                    videoRef.current.src = url;
                    videoRef.current.load();
                }
                else {
                    alert("Cannot Play type MpegUrl.");
                }

            }}>
                Load
            </Button>
            <Button onPress={()=> {
                videoRef.current.play();
            }}>
                Play
            </Button>
        </View>
    );
}
export default SoundUIPlayer;

