import React, { useEffect, useLayoutEffect } from "react";
import { StyleSheet, View, Modal } from "react-native";
import { TouchableRipple, Title, Surface } from "react-native-paper";
import FlipCard, { FlipSide } from "../animations/flip";
import { useIsFemale } from "../../hooks/use-prefer";
import Animated, {FadeOutDown, FadeInUp, Layout, Easing} from "react-native-reanimated";
import { femaleImgUri, maleImgUri } from "./icon";
import { useTranslation } from "react-i18next";
import { Card } from "react-native-paper";

export const CardView = ({ title, iconUri, style={}, titleStyle={}, iconStyle={}}) => (
    <View style={[styles.card, style]}>
        <Card.Cover style={[styles.icon, iconStyle]} source={iconUri} />
        <Title style={[styles.title, titleStyle]}>{title}</Title>
    </View>
)

export const SweetCard = (props)=> {
    const { t } = useTranslation();
    return <CardView {...props} iconUri={femaleImgUri} title={t('Sweet with love')} />
};

export const FantasyCard = (props)=> {
    const { t } = useTranslation();
    return <CardView {...props} iconUri={maleImgUri} title={t('Fantasy, action')} />
}

export function PreferAnim() {
    const [isFemale] = useIsFemale();
    const { t } = useTranslation();

    return (
        <FlipCard
            side={isFemale ? FlipSide.FRONT : FlipSide.BACK}
            modal={true}
            style={styles.flip}
            slideStyle={styles.slide}
            frontStyle={styles.front}
            backStyle={styles.back}
            front={<SweetCard />}
            back={<FantasyCard />}
        />
    )
}

const styles = StyleSheet.create({
    flip: {
        height: 300,
        width: 300,
    },
    slide: {
        width: 300,
        height: 300,
    },
    front: {
        backgroundColor: 'rgb(188, 69, 152)',
    },
    back: {
        backgroundColor: 'rgb(187, 248, 198)',
    },
    card: {
        alignItems: 'center',
        justifyContent: 'center',
        rowGap: 20,
        backgroundColor: 'transparent',
    },
    icon: {
        width: 100,
        height: 100,
        backgroundColor: 'transparent',
    },
    title: {
        fontWeight: 'bold',
    }
});