// preference.js

import * as React from 'react';
import { View, StyleSheet } from 'react-native';
import { Button, Caption } from 'react-native-paper';
import { useTranslation } from 'react-i18next';
import { HzLogo } from '../comps/logo';
import { SweetCard, FantasyCard } from '../comps/prefer/anim';
import { Surface, TouchableRipple } from 'react-native-paper';

import { MALE, FEMALE } from '../constants/session';
import { usePrefer } from '../hooks/use-prefer';
import { useTheme } from 'react-native-paper';


function PreferItem({ type, checked, onPress }) {
  const theme = useTheme();
  const elevation = checked ? 4: 1;
  const cardStyle = checked ? {
    backgroundColor: theme.colors.surface,
    borderRadius: 10,
  }: {
    backgroundColor: theme.colors.surface,
    borderRadius: 10,
  };
 
  return (
    <TouchableRipple style={styles.preferItem} onPress={() => onPress(type)} >
      <Surface elevation={elevation} style={cardStyle}>
        {type === FEMALE ? 
          <SweetCard style={{rowGap: 0}} titleStyle={[
            styles.preferTitle,
            {color: theme.colors.primary}
          ]} /> 
          : 
          <FantasyCard style={{rowGap: 0}} titleStyle={[
            styles.preferTitle,
            {color: theme.colors.accent},
          ]} />}
      </Surface>
    </TouchableRipple>
  )
}

function PreferenceScreen({ route, navigation }) {
  const { t } = useTranslation();
  const theme = useTheme();
  const [value, setPrefer] = usePrefer();
  const [choice, setChoice] = React.useState(value);

  return (
    <View style={[styles.container, {backgroundColor: theme.colors.background}]}>
      <HzLogo style={styles.logo} />
      <Caption style={styles.caption}>{t('Which genre do you like ?')}</Caption>
      <View style={styles.prefers}>
        <PreferItem type={FEMALE} checked={choice == FEMALE} onPress={setChoice} />
        <PreferItem type={MALE} checked={choice == MALE} onPress={setChoice} />
      </View>
      <View style={styles.links}>
        <Button style={styles.link}>{t('Terms & Conditions')}</Button>
        <Button style={styles.link}>{t('Privacy Policies')}</Button> 
        <Button 
          style={styles.next} 
          mode="contained"
          onPress={() => {
            setPrefer(choice)
          }}
          >{t('Ok')}</Button> 
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    gap: 10,
    paddingTop: 100,
    paddingBottom: 200,
  },
  logo: { 
    height: 80,
  },
  caption: {
    fontSize: 12,
  },
  links: {
    flex: 1,
    width: '100%',
    alignItems: 'center',
    justifyContent: 'center',
  },
  link: {
  },
  next: {
    width: '70%',
  },
  prefers: {
    gap: 30,
    flexDirection: 'row',
  },
  preferItem: {
    width: 130,
    height: 130,
  }, 
  preferTitle: {
    fontSize: 12,
  }
});

export default PreferenceScreen;
