
import * as React from 'react';
import { View, Image, StyleSheet } from 'react-native';
import {
    Subheading,
    TouchableRipple,
} from 'react-native-paper';

import { coverUri } from '../../utils/uri';

const StoryItem = React.memo(({ navigation, id, code, title, cover, external_cover_url }) => (
    <TouchableRipple style={styles.container} onPress={()=> navigation.push('Details', {
        storyCode: code,
    }) }>
        <>
        <Image source={{uri: coverUri(cover, external_cover_url)}} style={styles.image} resizeMode={'cover'} />
        <Subheading>{title}</Subheading>
        </>
    </TouchableRipple>
));


const styles = StyleSheet.create({
    container: {
        flex: 1,
        marginVertical: 10,
        flexDirection: 'column'
    },
    image: {
        flex: 1,
        aspectRatio: 2 / 3,
        marginRight: 10,
        borderRadius: 5,
    },
});

export default StoryItem;