import { memo } from "react"
import { View, Image, StyleSheet } from 'react-native';
import { Title, Caption } from 'react-native-paper';
import { useHeaderHeight } from '@react-navigation/elements';
import Animated from "react-native-reanimated";
import { useTranslation } from "react-i18next";
import { GradientCover } from '../covers';
import { ViewsIcon, LikesIcon } from '../icons';
import Tags from '../tags';
import { SCROLL_HEAD_HEIGHT } from "../animations/scroll"
import { coverUri } from "../../utils/uri"

function ScrollHeader({ story, tintColor, scrollHeaderTransStyle }) {
    const { t } = useTranslation();
    const uri = coverUri(story.cover, story.external_cover_url)
    const tags = [...story.categories, { name: story.full ? t('Full') : t('Coming') }]

    const headerHeight = useHeaderHeight();

    return (
        <GradientCover uri={uri} style={[
            { height: SCROLL_HEAD_HEIGHT },]
        }>
            <Animated.View style={[
                { marginTop: headerHeight },
                styles.header,
                scrollHeaderTransStyle]}>
                <Image
                    source={uri}
                    resizeMode={"cover"}
                    style={styles.cover} />
                <View style={styles.meta}>
                    <Title style={[
                        styles.title, {
                            color: tintColor,
                        }
                    ]}> {story.title} </Title>
                    <Caption>{story.author.name}</Caption>
                    <Tags tags={tags} />
                    <View style={styles.icons}>
                        <ViewsIcon views={story.views} />
                        <LikesIcon likes={story.likes} />
                    </View>
                </View>
            </Animated.View>
        </GradientCover>
    )
}


const styles = StyleSheet.create({
    header: {
        marginHorizontal: 10,
        flexDirection: 'row',
        gap: 20,
        height: 145,
    },
    title: {
        color: 'white'
    },
    cover: {
        width: 112,
        height: 145,
        borderRadius: 5,
    },
    meta: {
        flexDirection: 'column',
        gap: 10,
    },
    icons: {
        flexDirection: 'row',
        gap: 20,
    },
});

export default memo(ScrollHeader);