// section list | horizontal list

import * as React from 'react';
import {
    View,
    StyleSheet,
    FlatList,
    SectionList,
    RefreshControl,
} from 'react-native';
import { useTranslation } from 'react-i18next';
import { Text, Subheading, Headline, useTheme } from 'react-native-paper';
import { useQuery, NetworkStatus } from "@apollo/client";
import { usePrefers } from '../../hooks/use-prefer';

import { Error } from '../error';
import { ListLoadmoreIndicator, ScreenLoadingIndicator } from '../indicators';
import { ListHeader, ItemSeparator, ListEmpty } from '../list';
import StoryItem from './StoryItem';
import Collection from './Collection';

import flatten from '../../utils/flatten';

import { GET_TOP_COLLECTIONS } from '../../api/graphql/collection';


const StoriesCollections = ({ route, navigation }) => {
    const theme = useTheme();
    const { t } = useTranslation();
    const segments = usePrefers();

    const { loading, error, data, fetchMore, refetch, networkStatus } = useQuery(GET_TOP_COLLECTIONS, {
        notifyOnNetworkStatusChange: true,
        variables: {
            segments: segments
        },
    });


    if (error) return <Error
        msg={error.message}
        onRefresh={() => refetch()}
    />

    if (loading) return <ScreenLoadingIndicator />
    
    let collections = flatten(data.collections)
    // collections = [collections[0]];
    // console.log(collections);

    return (
        <View style={styles.container}>
            {collections.map(c => <Collection 
                                    key={c.id} {...c} 
                                    //view={'gridlarge'}
                                    t={t}
                                    navigation={navigation} 
                                    theme={theme} />)}
        </View>
    )
};


const styles = StyleSheet.create({
    container: {
        // flex: 1,
        flexDirection: 'column',
        paddingHorizontal: 10,
        paddingBottom: 10,
        rowGap: 20,
        // backgroundColor: 'orange',
    },
    footer: {
        flex: 1,
        paddingBottom: 10,
        alignItems: 'center',
        justifyContent: 'center',
    },

});
export default StoriesCollections;

