import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { View, StyleSheet } from 'react-native';
import { Caption, ActivityIndicator } from 'react-native-paper';

const ListLoadmoreIndicator = React.memo((props) => {
  const { t } = useTranslation();
  return (
    <View style={styles.container}>
      <ActivityIndicator />
      <Caption>{t('Fetching more..')}</Caption>
    </View>
  );
});

const ScreenLoadingIndicator = React.memo(props => {
  return (
    <View style={styles.loading}>
      <ActivityIndicator size="large" />
    </View>
  )
});

const LazyLoadingIndicator = React.memo(({msg}) => {
  return (
    <View style={styles.loading}>
      <ActivityIndicator />
      <Caption>{msg}</Caption>
    </View>
  )
});


export { 
  ListLoadmoreIndicator,
  ScreenLoadingIndicator,
  LazyLoadingIndicator 
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loading: {
    flex: 1,
    flexGrow: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
});
