import { useState } from "react";
import { LikeButton } from "./buttons";
import { useMutation, useQuery } from "@apollo/client";

import { LIKE_STORY, GET_STORY_LIKES } from "../api/graphql/like";

function Like({ liked, storyId }) {
    const [isLiked, setLiked] = useState(liked);
    const [like, { data, loading, error, reset }] = useMutation(LIKE_STORY, {
        refetchQueries: [
            {
                query: GET_STORY_LIKES, 
                variables: {
                    storyId
                },
            }
        ],
    })
    
    return (
        <LikeButton liked={isLiked} onPress={() => {
            setLiked(v => !v)
            like({
                variables: {storyId}
            })
        }} />
    )
}

export default Like;