import { useEffect, useState } from 'react';
import { LinearGradient } from 'expo-linear-gradient';
import { useIsDark } from '../../hooks/use-theme';
import Animated, { 
    useAnimatedStyle, 
    interpolateColor, 
    useSharedValue, 
    withTiming, 
    cancelAnimation } from 'react-native-reanimated';

import colorFor from '../utils/colors';
const defaultUri = require('./images/default.jpg');

const darkGradient = [
    'rgba(0,0,0,0.3)',
    'rgba(0,0,0,0.4)',
    'rgba(0,0,0,0.3)',
];

const lightGradient = [
    'rgba(255,255,255,0.3)',
    'rgba(255,255,255,0.7)',
    'rgba(255,255,255,0.8)',
];


const GradientCover = ({ uri, style, contentStyle, imageStyle, children }) => {
    const progress = useSharedValue(0);
    const [isDark] = useIsDark();
    const [color, setColor] = useState('white');
    
    const rStyle = useAnimatedStyle(() => {
        return {
          backgroundColor: interpolateColor(
            progress.value,
            [0, 1],
            [isDark ? 'black': 'white', color],
            'RGB'
          ),
        };
    });

    async function fetchColor(link) {
        try {
            const { background, primary } = await colorFor(link);
            setColor(isDark ? primary : background);
            progress.value = withTiming(1 - progress.value, { duration: 1000 });
        }
        catch(e) {}
    }

    useEffect(()=> {
        fetchColor(uri || defaultUri)
        return () => {
            cancelAnimation(progress)
        }
    }, [uri, isDark]);

    return (
        <Animated.View style={[{
            height: 200,
            justifyContent: 'center', 
            alignItems: 'center',
        },  rStyle, style]}>
            <LinearGradient
                    colors={isDark ? darkGradient: lightGradient}
                    locations={[0, 0.4, 0.9]}
                    style={[{
                        width: '100%',
                        height: '100%',
                    },contentStyle]}>
                    {children}
                </LinearGradient>
        </Animated.View>
    );
};


export {
    GradientCover,
}