import { View, StyleSheet } from 'react-native';
import { useTranslation } from "react-i18next";
import Icon from 'react-native-vector-icons/Ionicons';
import { Caption, IconButton, TouchableRipple, Colors, useTheme } from 'react-native-paper';
import ScreenIds from '../../constants/screens';

function LinkItem({navigation, icon, circleColor, title, onPress}) {
    const theme = useTheme();

    return (
        <TouchableRipple style={styles.item} onPress={onPress}>
            <>
                <View style={[styles.circle, {backgroundColor: circleColor}]}>
                    <Icon size={22} color={theme.colors.accent} name={icon}/>
                </View>
                <Caption>{title}</Caption>    
            </>
        </TouchableRipple>
    )
}

function FeatureLinks({ navigation }) {
    const {t} = useTranslation();
    return (
        <View style={styles.container}>
            <LinkItem 
                navigation={navigation}
                icon={'stats-chart'}
                circleColor={'#FDF49C'}
                title={t('Ranking')}
                onPress={()=> navigation.navigate(ScreenIds.Chart)}
            />
            <LinkItem 
                navigation={navigation}
                icon={'color-palette-outline'}
                circleColor={'#D3F1FF'}
                title={t('Genre')}
                onPress={()=> navigation.push(ScreenIds.Channel)}
            />
            <LinkItem 
                navigation={navigation}
                icon={'fitness-outline'}
                circleColor={'#FECD80'}
                title={t('Point')}
                onPress={()=> navigation.navigate(ScreenIds.Wallet)}
            />
            <LinkItem 
                navigation={navigation}
                icon={'headset'}
                circleColor={'#BBF8C6'}
                title={t('Music')}
                onPress={()=> navigation.navigate(ScreenIds.Music)}
            />
        </View>
    )
}

const styles = StyleSheet.create({
    container: {
    //   flex: 1,
      justifyContent: "space-between",
      alignItems: "center",
      flexDirection: 'row',
    //   backgroundColor: 'red',
      paddingVertical: 10,
      paddingHorizontal: 30,
      gap: 5,
    },
    item: {
        flex: 1,
        alignItems: "center",
        // backgroundColor: 'green',
        padding: 5,
    },
    circle: {
        alignItems: "center",
        justifyContent: "center",
        width: 50,
        height: 50,
        backgroundColor: 'violet',
        padding: 3,
        borderRadius: 30,
    }
  });

export default FeatureLinks;