import React from "react";
import { StyleSheet, View } from "react-native";
import { TouchableRipple, Avatar } from "react-native-paper";
import FlipCard, { FlipSide} from "../animations/flip";
import { useIsFemale } from "../../hooks/use-prefer";
import { femaleImgUri, maleImgUri } from "./icon";

const CardView = ({size, iconUri}) => (
    <Avatar.Image size={size} source={iconUri} style={{backgroundColor: 'transparent'}}/>
)

export function PreferSwitch({size=30}) {
    const [isFemale, switchPrefer] = useIsFemale();
    const style = { width: size, height: size};

    return (
        <TouchableRipple onPress={switchPrefer}>
            <View style={styles.container}>
                <FlipCard
                    side={isFemale ? FlipSide.FRONT: FlipSide.BACK}
                    style={[styles.flip, style]}
                    slideStyle={[styles.slide, style]}
                    frontStyle={styles.front}
                    backStyle={styles.back}
                    front={<CardView size={size} iconUri={femaleImgUri} />}
                    back={<CardView size={size} iconUri={maleImgUri}/>}
                />
            </View>
        </TouchableRipple> 
    )
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: 'transparent',
        alignItems: 'center',
        justifyContent: 'center',
    },
    flip: {
        height: 40,
        width: 40,
        backgroundColor: 'transparent',
    },
    slide: {
        width: 40,
        height: 40,
        backgroundColor: 'transparent',
    },
    front: {
        backgroundColor: 'rgb(188, 69, 152)',
    },
    back: {
        backgroundColor: 'rgb(187, 248, 198)',
    },
});