import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { View, StyleSheet } from 'react-native';
import { Caption, Button } from 'react-native-paper';

const ItemSeparator = React.memo((props) => <View style={styles.separator} />);

const ListHeader = React.memo((props) => <></>);

const ListEmpty = React.memo(({ onRefresh }) => {
  const { t } = useTranslation();
  return (
    <View style={styles.empty}>
      <Caption>{t('No data at the moment')}</Caption>
      <Button onPress={onRefresh}>{t('Refresh')}</Button>
    </View>
  );
});

const styles = StyleSheet.create({
  empty: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  separator: {
    height: 1,
    backgroundColor: 'grey',
    marginHorizontal: 10,
  },
});

export { ItemSeparator, ListHeader, ListEmpty };
