import { useGlobals } from "../contexs/global";
import { useTranslation } from "react-i18next";

const LANG_VI = 'vi';
const LANG_EN = 'en';

export const useIsVi = () => {
  const [_, dispatch] = useGlobals();
  const { t, i18n } = useTranslation();
  
  const switchLanguage = () => {
    const target = i18n.language == LANG_VI ? LANG_EN: LANG_VI;
    i18n.changeLanguage(target);
    
    dispatch({
      type: 'setSession',
      fields: {
        language: target,
      },
      save: true,
    });
  };

  return [i18n.language == LANG_VI, switchLanguage];
};
