import * as path from './_patch';
import { NavigationContainer } from '@react-navigation/native';
import { ApolloProvider } from '@apollo/client';
import { client } from './api/client';
import { StatusBar } from 'expo-status-bar';
import { reloadAsync } from 'expo-updates';
import * as React from 'react';
import { AppState } from 'react-native';
import * as SplashScreen from 'expo-splash-screen';

import { View, StyleSheet, ActivityIndicator, I18nManager as RNI18nManager } from 'react-native';
import { Provider as PaperProvider } from 'react-native-paper';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import Ionicons from 'react-native-vector-icons/Ionicons';

import { initialState, reducer, StateProvider, useGlobals } from './contexs/global';
import AppStack from './navigation/appStack';
import AuthStack from './navigation/authStack';
import { initialPlayerState, playerReducer, PlayerProvider, usePlayerDispatch } from './contexs/player';

import { linking, fallback } from './navigation/linking';
import themes from './theme';
import { initI18n } from './i18n';
import Storer from './utils/storer';
import { DateUtils, Platform } from './utils';
import { SESSION_KEY } from './constants/session';
import { PLAYER_KEY } from './constants/player';
import { initLoggerService } from './services/rlog';

SplashScreen.preventAutoHideAsync();
initLoggerService();


const deepLink = Platform.isWeb ? null : linking; // Disable linking in web.

function ThemedApp({ children }) {
	const [{ session, day }, dispatch] = useGlobals();
	const playerDispatcher = usePlayerDispatch();
	const [isReady, setReady] = React.useState(false);
	const [appState, setAppState] = React.useState(AppState.currentState);
	const activeTheme = themes[session.theme];

	/** handle state change */
	React.useEffect(
		() => {
			// Handles screen focus and case when user reopens app one day later (Date has to be updated)
			const onStateChange = (nextAppState) => {
				if (appState.match(/active/) && nextAppState === 'active') {
					const nDate = DateUtils.toStr(new Date());
					if (nDate !== day) {
						dispatch({
							type: 'setDay',
							day: nDate
						});
					}
				}
				setAppState(nextAppState);
			};

			const stateListener = AppState.addEventListener('change', onStateChange);
			return () => {
				stateListener.remove();
			};
		},
		[appState, day, dispatch]
	);

	/** startup */
	React.useEffect(
		() => {
			(async () => {
				try {
					const session = await Storer.get(SESSION_KEY);
					if (session) {
						dispatch({
							type: 'setSession',
							fields: { ...session }
						});
					}

					const playerState = await Storer.get(PLAYER_KEY);
					if (playerState) {
						playerDispatcher({
							type: 'setState',
							fields: { ...playerState }
						})
					}
					if (session) {
						initI18n(session.language);
					}

				} finally {
					setReady(true);
				}
			})();
		},
		[dispatch]
	);

	const onLayoutRootView = React.useCallback(async () => {
		if (isReady) {
			// This tells the splash screen to hide immediately! If we call this after
			// `setAppIsReady`, then we may see a blank screen while the app is
			// loading its initial state and rendering its first pixels. So instead,
			// we hide the splash screen once we know the root view has already
			// performed layout.
			await SplashScreen.hideAsync();
		}
	}, [isReady]);

	if (!isReady) {
		return null;
	}

	console.log("app start with: ", session.prefer)

	return (
		<SafeAreaProvider>
			<PaperProvider
				theme={activeTheme}
				settings={{
					icon: (props) => <Ionicons {...props} />
				}}
			>
				<NavigationContainer
					theme={activeTheme}
					linking={deepLink}
					fallback={fallback}
					onReady={onLayoutRootView}>
					{
						session.prefer ? <AppStack />
							: <AuthStack />
					}
				</NavigationContainer>
			</PaperProvider>
		</SafeAreaProvider>
	);
}

export default function RootApp() {
	return (
		<ApolloProvider client={client}>
			<StateProvider initialState={initialState} reducer={reducer}>
				<PlayerProvider initialState={initialPlayerState}
					            reducer={playerReducer}>
					<ThemedApp />
				</PlayerProvider>
			</StateProvider>
		</ApolloProvider>
	);
}

const styles = StyleSheet.create({
	container: {
		flex: 1,
		backgroundColor: '#fff',
		alignItems: 'center',
		justifyContent: 'center'
	}
});
