import * as Linking from 'expo-linking';
import { Text } from 'react-native-paper';

const prefix = Linking.createURL('/');

const linking = {
  prefixes: [prefix],
  config: {
    // initialRouteName: 'Home',
    screens: {
      Search: {
        path: 'search',
        exact: true,
      },
      Create: 'new',
      /** bottom tabs */
      AppTabs: {
        // initialRouteName: 'Home',
        screens: {
          /* home tab */
          HomeStack: {
            initialRouteName: 'Home',
            screens: {
              Home: {
                path: '',
                exact: true,
              },
              Details: {
                path: 'stories/:storyCode',
                exact: true,
              },
              Reading: {
                path: 'stories/:storyCode/:chapterCode',
                exact: true,
              },
              Channel: {
                path: 'categories',
                exact: true,
              },
            },
          },
          /* chart tab */
          Chart: { exact: true, path: 'chart' },
          /* explorer tab */
          Explorer: { exact: true, path: 'explorer' },
          /* me tab */
          LibraryStack: {
            initialRouteName: 'Library',
            screens: {
              Library: { exact: true, path: 'library' },
              Inboxes: { exact: true, path: 'inboxes' },
              Settings: { exact: true, path: 'settings' },
            },
          },
          // - end tab --
        },
      },
      NotFound: '*',
    },
  },
};

const fallback = <Text>Loading...</Text>;

export { linking, fallback };
