// settings.js

import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { View, Text, ScrollView, StyleSheet } from 'react-native';
import {
  Button,
  Switch,
  Avatar,
  Title,
  Caption,
  Paragraph,
  Divider,
  Subheading,
  List,
  MD3Colors,
  TouchableRipple,
} from 'react-native-paper';

import ClearCache from '../comps/utils/cache';
import LogoutButton from '../comps/utils/logout';
import AutoSignButton from '../comps/utils/setToken';

import { BMVolumeSetting } from '../comps/players/comps/bm-volume';
import { useIsDark } from '../hooks/use-theme';
import { useIsFemale } from '../hooks/use-prefer';
import { useIsVi } from '../hooks/use-language';

function SettingScreen() {
  const { t } = useTranslation();
  const [isFemale, switchPrefer] = useIsFemale();
  const [isDark, switchTheme] = useIsDark();
  const [isVi, switchLanguage] = useIsVi();

  return (
    <ScrollView style={styles.container}>

      <View style={styles.row}>
            <Subheading>{t('English <-> Vietnamese')}</Subheading>
            <Switch value={isVi}  onValueChange={switchLanguage} />
      </View>


      <View style={styles.row}>
            <Subheading>{t('Dark Mode')}</Subheading>
            <Switch value={isDark}  onValueChange={switchTheme} />
      </View>
      <View style={styles.row}>
            <Subheading>{t('Switch -> MALE')}</Subheading>
            <Switch value={isFemale }  onValueChange={switchPrefer} />
      </View>
      <BMVolumeSetting />
      <AutoSignButton />
      <LogoutButton />

    </ScrollView>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    elevation: 2,
    padding: 16,
  },
  row: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    paddingTop: 16,
    paddingHorizontal: 16,
    width: '100%'
  },
 
});

export default SettingScreen;
