import {
  ApolloClient,
  InMemoryCache,
  createHttpLink,
  defaultDataIdFromObject,
} from '@apollo/client';
import { setContext } from '@apollo/client/link/context';


import possibleTypes from './possibleTypes.json';
import queries from './queries.json';
import flatten from '../utils/flatten';
import uniqBy from 'lodash.uniqby';

import Storer from '../utils/storer';
import { TOKEN_KEY } from '../constants/session';

/** create entity types from possibleTypes */
const entityTypes = possibleTypes.GenericMorph.map(item => `${item}Entity`)
const mergedEntityTypes = entityTypes.reduce((acc, curr) => (
  acc[curr] = { fields: { attributes: { merge: true } } }, acc), {}
);

/** crate merge rules for all loadmorable types. */
const mergeRules = queries.keys.reduce((acc, curr) => (
  acc[curr] = {
    keyArgs: ['filters', 'sort'],
    merge(existing = { data: [], meta: {} }, incoming) {
      const { meta, data } = incoming;
      const merged = [...existing.data, ...data];
      const uniq = uniqBy(merged, '__ref');
      return {
        data: uniq,
        meta: { ...meta }
      }
    }
  }, acc), {}
);

const cacheOptions = {
  possibleTypes: possibleTypes,
  /** merge attributes to one entity in cache. */
  typePolicies: {
    ...mergedEntityTypes,
    Query: {
      fields: {
        ...mergeRules,
      }
    },
  },

  dataIdFromObject(response) {
    /** customize cache-key-id */
    switch (response.__typename) {
      case 'TodoEntity': return `TodoEntity:${response.id}`;
      default: return defaultDataIdFromObject(response);
    }
  },
}

const httpLink = createHttpLink({
  uri: 'http://localhost:1337/graphql',
});

const authLink = setContext( async(_, { headers }) => {
  const token = await Storer.get(TOKEN_KEY);

  return {
    headers: {
      ...headers,
      authorization: token ? `Bearer ${token}` : "",
    }
  }
});

// Initialize Apollo Client
export const client = new ApolloClient({
  link: authLink.concat(httpLink),
  cache: new InMemoryCache(cacheOptions),
  connectToDevTools: true // TODO: remove in production.
});