import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { View, StyleSheet } from 'react-native';
import { Caption, Button } from 'react-native-paper';


const Error = React.memo(({ msg, onRefresh }) => {
  const { t } = useTranslation();
  return (
    <View style={styles.error}>
      <Caption>{ msg || t('Have error, try again ?')}</Caption>
      <Button onPress={onRefresh}>{t('Retry')}</Button>
    </View>
  );
});

const NotFound = React.memo(({ msg, back }) => {
  const { t } = useTranslation();
  return (
    <View style={styles.error}>
      <Caption>{ msg || t('Not found :(')}</Caption>
      <Button onPress={back}>{t('Back')}</Button>
    </View>
  );
});

const styles = StyleSheet.create({
    error: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
});

export { Error, NotFound };
