
import * as React from 'react';
import { View, FlatList, StyleSheet, RefreshControl } from 'react-native';
import { Error } from '../error';
import {
  Text,
  List,
  Caption,
  Button,
  useTheme,
  Divider,
  ActivityIndicator,
} from 'react-native-paper';

import { useQuery, NetworkStatus } from "@apollo/client";
import { GET_TODOS, } from '../../api/test/todos';

import flatten from '../../utils/flatten';

import { 
  ListLoadmoreIndicator, 
  ScreenLoadingIndicator 
} from '../indicators';
import { ListHeader, ItemSeparator, ListEmpty } from '../list';
import { useTranslation } from 'react-i18next';

const TodoItem = ({ id, title, hours }) => (
  <List.Item
    style={styles.item}
    title={title}
    description={`work hours ${hours}`}
    left={(props) => <List.Icon {...props} icon="notifications" />}
    right={(props) => <Caption>{`id:${id}`}</Caption>}
    onPress={() => {
      alert('selected.');
    }}
  />
);


export function TodoList() {
  const theme = useTheme();
  const { t } = useTranslation();

  const { loading,
    error,
    data,
    fetchMore,
    refetch, networkStatus } = useQuery(GET_TODOS, {
      fetchPolicy: 'cache-first', // 'cache-and-network, no-cache, cache-and-network, cache-only, cache-first',

      /** network-only, make dupplicate item in cache ? */
      notifyOnNetworkStatusChange: true,
      variables: {
        start: 0,
        limit: 10
      },
    });

  if (error) return <Error
    msg={error.message}
    onRefresh={() => refetch()}
  />

  const execFetchMore = () => {
    const { todos: { meta: { pagination } } } = data;
    if (pagination.page < pagination.pageCount) {
      fetchMore({
        variables: {
          start: data.todos.data.length
        },
      });
    }
  }

  const renderFooter = (props) => {
    let noMore = false
    if (!loading && !error) {
      const { todos: { meta: { pagination: {
        page, pageCount
      } } } } = data;
      noMore = (page === pageCount)
    }
    return (
      <View style={styles.footer}>
        {networkStatus == NetworkStatus.fetchMore && <ListLoadmoreIndicator />}
        {noMore && <Caption>No more items at the moment</Caption>}
      </View>
    )
  };

  return (
    <>
      {networkStatus === NetworkStatus.loading ? (
        <ScreenLoadingIndicator />
      ) : (
        <FlatList
          contentContainerStyle={styles.list}
          data={flatten(data.todos)}
          ListHeaderComponent={ListHeader}
          ListFooterComponent={renderFooter}
          ListEmptyComponent={() => <ListEmpty onRefresh={() => refetch()} />}
          ItemSeparatorComponent={ItemSeparator}
          renderItem={({ item }) => (
            <TodoItem
              title={item.title}
              hours={item.hours}
              id={item.id}
            />
          )}
          keyExtractor={(item) => item.id}
          onEndReachedThreshold={0.2}
          onEndReached={execFetchMore}
          refreshControl={
            <RefreshControl
              refreshing={networkStatus === NetworkStatus.refetch}
              onRefresh={() => refetch()}
              title={t('loading todos')}
              titleColor={theme.colors.text}
              colors={theme.colors.primary}
              tintColor={theme.colors.primary}
            />
          }
        />
      )}
    </>
  )
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  empty: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  footer: {
    flex: 1,
    paddingBottom: 10,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loading: {
    flex: 1,
    flexGrow: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  list: {
    flexGrow: 1,
  },
  item: {
    flex: 1,
    padding: 20,
    fontSize: 15,
    marginTop: 5,
  },
});

/**
 * NetworkStatus
 
  loading = 1,
  setVariables = 2,
  fetchMore = 3,
  refetch = 4,
  poll = 6,
  ready = 7,
  error = 8,
 */