import { createNativeStackNavigator } from '@react-navigation/native-stack';
import * as React from 'react';

import PreferenceScreen from '../screens/preference';
import LoginScreen from '../screens/login';
import RegisterScreen from '../screens/register';

import ScreenIds from '../constants/screens';

const Stack = createNativeStackNavigator();

function AuthStack() {
  return (
    <Stack.Navigator>
      <Stack.Screen name={ScreenIds.Preference} component={PreferenceScreen} />
      <Stack.Screen name={ScreenIds.Login} component={LoginScreen} />
      <Stack.Screen name={ScreenIds.Register} component={RegisterScreen} />
    </Stack.Navigator>
  );
}

export default AuthStack;
