import { StyleSheet, Text, Button, ViewStyle, View } from 'react-native';
import React, { useEffect, useState, useLayoutEffect } from 'react';
import { Modal } from 'react-native-paper';

import Animated, {
    interpolate,
    Extrapolate,
    useSharedValue,
    withTiming,
    withSpring,
    useAnimatedStyle,
    Easing
} from 'react-native-reanimated';

export const FlipSide = {
    FRONT: 0,
    BACK: 1
}

const ReanimatedFlip = ({
    front,
    back,
    side=null,
    style={},
    slideStyle={},
    frontStyle={},
    backStyle={},
    duration = 500,
    easing = Easing.bezier(0.5, 0.01, 0, 1),
    modal = false,
}) => {
    const [visible, setVisible] = useState(true);
    const spin = useSharedValue(0);
    const fStyle = useAnimatedStyle(() => {
        const spinV = interpolate(spin.value, [0, 1], [0, 180]);
        return {
            transform: [{
                rotateY: withTiming(`${spinV}deg`, { duration, easing })
            }]
        }
    });
    const bStyle = useAnimatedStyle(() => {
        const spinV = interpolate(spin.value, [0, 1], [180, 360]);
        return {
            transform: [{
                rotateY: withTiming(`${spinV}deg`, { duration,  easing })
            }]
        }
    });

    const hide = ()=> {
        setVisible(false);
    }

    useEffect(() => {
        spin.value = side
        setVisible(true)

        const autoHide = setTimeout(()=> {
            setVisible(false)
        }, 1000)
        return ()=> {
            clearTimeout(autoHide);
        }
    }, [side])


    const animated = (
        <View style={[styles.container, style]}>
            <Animated.View style={[styles.slide, styles.front, slideStyle, frontStyle, fStyle]}>
                {front}
            </Animated.View>
            <Animated.View style={[styles.slide, styles.back, slideStyle, backStyle, bStyle]}>
                {back}
            </Animated.View>
        </View>
    )
    
    return modal ? (
        <Modal style={styles.modal} visible={visible} onDismiss={hide}>
            {animated}
        </Modal>
    ): animated
}

const styles = StyleSheet.create({
    container: {
        justifyContent: "center",
        alignItems: "center",
    },
    modal: {
        justifyContent: "center",
        alignItems: "center",
    },
    slide: {
        height: 400,
        width: 250,
        borderRadius: 16,
        backfaceVisibility: "hidden",
        justifyContent: "center",
        alignItems: "center",
        position: "absolute",
    },
    front: {
    },
    back: {
    },
});

export default ReanimatedFlip;