import { useGlobals } from "../contexs/global";

const THEME_DARK = 'dark';
const THEME_LIGHT = 'light';

export const useIsDark = () => {
  const [{ session: { theme } }, dispatch] = useGlobals();
 
  const switchTheme = () => {
    dispatch({
      type: 'setSession',
      fields: {
        theme: theme === THEME_DARK ? THEME_LIGHT : THEME_DARK,
      },
      save: true,
    });
  };

  return [theme === THEME_DARK, switchTheme];
};
