import PlatformUtils from '../utils/platform';
import * as Sentry from "@sentry/react";

export const initLoggerService = ()=> {
    try {
        console.log('logger: init.');
        Sentry.init({
            dsn: "https://0f1f374020434793a7e50590ab89bffe@o4505243895332864.ingest.sentry.io/4505243896905728",
        });
    }
    catch(e) {
        console.log(e);
    }
}


export const log = (...args) => {
    console.log(args);
    try {
        Sentry.captureException(args);
    }
    catch(e) {
        console.log(e);
    }
};

export const logException = (e) => {
    try {
        Sentry.captureException(e);
    }
    catch(_e) {
        console.log(e);
    }
};

