import { useGlobals } from "../contexs/global";
import { MALE, FEMALE, BOTH } from "../constants/session";

export const usePrefers = () => {
  const [{ session }] = useGlobals();
  return session.prefer == MALE ? [ MALE, BOTH]: [FEMALE, BOTH];
}

export const usePrefer = () => {
  const [{ session }, dispatch] = useGlobals();

  const setPrefer = (value) => {
    dispatch({
      type: 'setSession',
      fields: {
        prefer: value,
      },
      save: true, 
    })
  }
  return [session.prefer, setPrefer];
}

export const useIsFemale = () => {
  const [{ session }, dispatch] = useGlobals();
  
  const switchPrefer = () => {
    dispatch({
      type: 'setSession',
      fields: {
        prefer: session.prefer === MALE ? FEMALE: MALE,
      },
      save: true, 
    })
  }

  return [session.prefer == FEMALE, switchPrefer];
}
  