

// https://docs.expo.dev/guides/google-authentication/
import { useEffect, useState } from 'react';
import * as AuthSession from 'expo-auth-session';
import * as WebBrowser from 'expo-web-browser';
import * as Google from "expo-auth-session/providers/google";

import { Image, StyleSheet, View } from 'react-native';
import { Button, Text } from 'react-native-paper';

WebBrowser.maybeCompleteAuthSession();

export function GoogleAuth() {
    const [token, setToken] = useState("");
	const [ user, setUser ] = useState(null);
  
    const [request, response, promptAsync] = Google.useAuthRequest({
      // androidClientId: "GOOGLE_GUID.apps.googleusercontent.com",
      // TODO: use config file
      webClientId: "247608820260-8liq8ksil2m6v84tgpq83sf9pdg3adlv.apps.googleusercontent.com",
      iosClientId: "247608820260-vi2bp28butlgpjjiff9tfnghdtdvdbca.apps.googleusercontent.com",
  
    });

    if (request) {
		console.log(
			'You need to add this url to your authorized redirect urls on your Google app: ' + request.redirectUri
		);
	}

    useEffect(() => {
        if (response?.type === "success") {
          setToken(response.authentication.accessToken);
          getUserInfo();
        }
      }, [response, token]);

      const getUserInfo = async () => {
        try {
          const response = await fetch(
            "https://www.googleapis.com/userinfo/v2/me",
            {
              headers: { Authorization: `Bearer ${token}` },
            }
          );
    
          const user = await response.json();
          console.log('user info', user);
          setUser(user);
        } catch (error) {
          // Add your own error handler here
        }
      };

      const handlePressAsync = async () => {
		const result = await promptAsync();
		if (result.type !== 'success') {
			alert('Uh oh, something went wrong');
			return;
		}
	};

      return (
		<View style={styles.container}>
			{user ? (
				<Profile user={user} />
			) : (
				<Button disabled={!request} onPress={handlePressAsync}>
					Sign in with Google
				</Button>
			)}
		</View>
      );
};



function Profile({ user }) {
	return (
		<View style={styles.profile}>
			<Image source={{ uri: user.picture }} style={styles.image} />
			<Text style={styles.name}>{user.name}</Text>
			<Text>ID: {user.id}</Text>
		</View>
	);
}


const styles = StyleSheet.create({
    container: {
      flex: 1,
      backgroundColor: "#fff",
      alignItems: "center",
      justifyContent: "center",
    },
    text: {
      fontSize: 20,
      fontWeight: "bold",
    },
	profile: {
		alignItems: 'center'
	},
	name: {
		fontSize: 20
	},
	image: {
		width: 100,
		height: 100,
		borderRadius: 50
	}
  });

  export default GoogleAuth;