import React, { useMemo } from 'react';
import { View, StyleSheet } from 'react-native';
import {
  Badge,
  IconButton,
  TouchableRipple,
  Colors, useTheme
} from 'react-native-paper';

import { ShareIcon, LikeIcon, MuteIcon } from './icons';

export const ShareButton = ({ url }) => (
  <TouchableRipple onPress={() => {
    console.log('Share');
  }}>
    <ShareIcon />
  </TouchableRipple>
)

export const LikeButton = ({ liked, onPress }) => (
  <TouchableRipple onPress={onPress}>
    <LikeIcon liked={liked}/>
  </TouchableRipple>
)

export const MuteButton = ({ muted, volume, onPress }) => (
  <TouchableRipple onPress={onPress}>
    <MuteIcon muted={muted} volume={volume}/>
  </TouchableRipple>
)

export const BadgeIcon = ({ onPress, icon, size, color, badges }) => {
  const theme = useTheme();

  return (
    <View>
      <IconButton icon={icon} size={size} color={color} onPress={onPress} />
      <Badge
        size={12}
        visible={badges > 0}
        style={[
          styles.badge,
          {
            color: theme.colors.text,
            backgroundColor: theme.colors.primary,
          },
        ]}
      >
        {badges}
      </Badge>
    </View>
  );
};

const styles = StyleSheet.create({
  item: {},
  badge: {
    position: 'absolute',
    fontSize: 8,
    top: 14,
    right: 8,
  },
});
