import { gql } from "@apollo/client";

export const GET_STORIES = gql`
query getStoriesByTag($tag: String!, $start: Int, $limit: Int) {
  stories(
    sort: ["views:DESC", "updatedAt:DESC"]
    filters: { categories: { code: { eq: $tag } } }
    pagination: { start: $start, limit: $limit }
  ) {
    meta {
      pagination {
        total
        page
        pageSize
        pageCount
      }
    }
    data {
      id
      attributes {
        full
        code
        title
        num_chapters
        views
        external_cover_url
        cover {
          data {
            attributes {
              url
            }
          }
        }
        categories {
          data {
            id
            attributes {
              name
              code
            }
          }
        }
        author {
          data {
            id
            attributes {
              code
              name
            }
          }
        }
        updatedAt
      }
    }
  }
}

`
