
import * as React from 'react';
import { View, Image, StyleSheet } from 'react-native';
import {
    Text,
    List,
    Caption,
    Button,
    useTheme,
    Divider,
    Title,
    Subheading,
    Chip,
    TouchableRipple,
} from 'react-native-paper';

import { NumChapIcon, ViewsIcon } from '../icons';
import Tags from '../tags';

import { coverUri } from '../../utils/uri';

const StoryItem = React.memo(({ navigation, id, code, title, categories, author, cover, external_cover_url, num_chapters, views }) => (
    <TouchableRipple style={styles.container} onPress={()=> navigation.push('Details', {
        storyCode: code,
    }) }>
        <>
        <Image source={{uri: coverUri(cover, external_cover_url)}} style={styles.image} resizeMode={'cover'} />
        <View style={styles.content}>
            <View style={styles.head}>
                <Subheading>{title}</Subheading>
                <Tags tags={categories} />
                <Caption>{author.name}</Caption>
            </View>
            <View></View>
            <View style={styles.footer}>
                <ViewsIcon views={views} />
                <NumChapIcon num={num_chapters} />
            </View>
        </View>
        </>
    </TouchableRipple>
));


const styles = StyleSheet.create({
    container: {
        flex: 1,
        marginVertical: 10,
        flexDirection: 'row',
        justifyContent: 'space-between',
    },
    footer: {
        flex: 1,
        flexDirection: 'row',
        gap: 20,
        justifyContent: 'flex-start',
    },
    image: {
        flex: 1,
        aspectRatio: 2 / 3,
        marginRight: 10,
        borderRadius: 5,
    },
    content: {
        flex: 2,
        justifyContent: 'space-between',
        flexDirection: 'column',
        // borderColor: "lightgray",
        // borderBottomWidth: 0.5,
    },
    head: {
        flex: 3,
        flexDirection: 'column',
        justifyContent: 'flex-start',
    }
});

export default StoryItem;