import * as React from 'react';
import { StyleSheet } from 'react-native';
import { List, Caption } from 'react-native-paper';

const InboxItem = ({ title, desc, updated }) => (
  <List.Item
    style={styles.item}
    title={title}
    description={desc}
    left={(props) => <List.Icon {...props} icon="notifications" />}
    right={(props) => <Caption>{updated}</Caption>}
    onPress={() => {
      alert('selected.');
    }}
  />
);

const styles = StyleSheet.create({
  item: {
    flex: 1,
    padding: 20,
    fontSize: 15,
    marginTop: 5,
  },
});

export default React.memo(InboxItem);
