import Slider from "@react-native-community/slider";
import { View, StyleSheet } from 'react-native';

import { MuteButton } from "../../buttons";
import { useBGMusic } from "../../../hooks/use-bm";
import { usePlayer } from "../../../contexs/player";

export function BMVolumeSetting() {

    const [ {bmState}, dispatch] = usePlayer();
    const {  setVolume, setMute } = useBGMusic();

    const isBMActive = bmState.isLoaded;
    const handleMute = ()=> {
        if (!isBMActive) {
            dispatch({
                type: 'setBMFields',
                fields: {
                    muted: !bmState.muted
                },
                save: true,
            })
        }
        else {
            setMute(!bmState.muted);
        }
    }

    const handleSetVolume = (v)=> {
        console.log("change to v; ", v);
        if (!isBMActive) {
            console.log("dispatch");
            dispatch({
                type: 'setBMFields',
                fields: {
                    volume: v
                },
                save: true,
            })
        }
        else {
            setVolume(v);
        }
    }

    return (
        <View style={styles.container}>
            <MuteButton
                muted={bmState.muted}
                volume={bmState.volume}
                onPress={handleMute} />
            <Slider
                style={styles.slider}
                value={bmState.volume}
                onValueChange={handleSetVolume}
            />
        </View>
    )
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
    },
    slider: {
        alignSelf: "stretch"
    },
});

