import { gql } from "@apollo/client";

const GET_STORY_DETAILS = gql`
    query getStoryByCode($code: String!) {
        storyByCode(code: $code) {
        data {
            id
            attributes {
            code
            title
            liked
            external_cover_url
            cover {
                data {
                attributes {
                    url
                }
                }
            }
            author {
                data {
                id
                attributes {
                    code
                    name
                }
                }
            }
            categories {
                data {
                id
                attributes {
                    name
                    code
                }
                }
            }
            last_chapters
            views
            likes
            num_chapters
            text
            full
            updatedAt
            first_chapter_code
            first_chapter_title
            }
        }
        }
    }
`
export { GET_STORY_DETAILS } 