import 'intl-pluralrules';
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

const resources = {
  en: {
    translation: {},
  },
  vi: {
    translation: {
      Home: ' Xut',
      Chart: '#DHChart',
      '#DHChart': '#DHChart',
      Library: 'Th Vin',
      Explorer: 'Khm Ph',
      Settings: 'Ci t',
      'Change Language': 'i Ngn Ng',
      'Which genre do you like ?': 'Bn thch th loi no hn ?',
      'Terms & Conditions': 'Tha thun ngi dng',
      'Privacy Policies': 'Tha thun quyn ring t',
      'Details': 'Chi tit',
      'Chapters': 'Chapters',
      'Last Chapter': 'Cp nhp n',
      'Latest': 'Mi nht',
      'Oldest': 'C nht',
      'Start Read': 'Bt u c',
      'Start': 'Bt u xem',
      'Back': 'Quay li',
      'Not found :(': 'Khng tm thy :(',
      'Full': 'Hon',
      'Coming': 'ang ra',
      'Like': 'Thch',
      'UnLike': 'B Thch',
      'Share': 'Chia s',
      'Logout': 'ng Xut',
      
    },
  },
};


function initI18n(lang) {
  i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources,
    lng: lang,
    fallbackLng: 'en',
    interpolation: {
      escapeValue: false, // react already safes from xss
    },
  });
}

export {
  i18n,
  initI18n,
}
