import React, { createContext, useContext, useReducer } from 'react';
import {
    MODE_MINI,
    PLAYER_KEY,
    BM_LOOPING_TYPE_ONE,
    BM_LOOPING_TYPE_ALL,
} from '../constants/player';
import Storer from '../utils/storer';


export const playerReducer = (state, action) => {
    let result = state;

    switch (action.type) {
        case 'setState':
            result = {
                ...state,
                ...action.fields,
            }
            break;
        case 'setPlaybackStat':
            result = {
                ...state,
                playbackStat: {
                    ...state.playbackStat,
                    ...action.payload,
                }
            }
            break;
        case 'setPlaybackError':
            result = {
                ...state,
                playbackError: action.payload
            }
            break;
        case 'playTrack':
            result = {
                ...state,
                track: action.payload.track,
                activeIndex: action.payload.index,
            }
            break;
        case 'setTracks':
            result = {
                ...state,
                tracks: action.payload,
            }
            break;
        case 'setBMError':
            result = {
                ...state,
                bmError: action.payload
            }
            break;
        case 'setBMState':
            result = {
                ...state,
                bmState: {
                    ...state.bmState,
                    ...action.payload,
                },
            };
            break;
        case 'setBMFields':

            console.log("receved: ", action.fields);

            result = {
                ...state,
                bmState: {
                    ...state.bmState,
                    ...action.fields,
                },
            }
            break;
        default:
            result = {
                ...state
            };
            break;
    }

    if (action.save) {
        Storer.set(PLAYER_KEY, result);
    }
    return result;
};

export const initialPlayerState = {
    mode: MODE_MINI,
    story: null,      /* active story */
    chapter: null,    /* active chapter */
    chapters: null,   /* active chapter list */

    /** audio state */
    tracks: [],     /* active track list */
    track: {},      /* active track */
    activeIndex: -1,        /* current index in playlist */
    playbackStat: {},     /* current audio-player state */

    /** bm music state */
    bmState: { /* mb state object */
        isLoaded: false,
        durationMillis: 0,
        positionMillis: 0, // -- to keep good performance, positionMillis will not updated interval.
        shouldPlay: false,
        isPlaying: false,
        isBuffering: false,
        isLooping: false,
        didJustFinish: false,
        progressUpdateIntervalMillis: 100, // will be ignore in BG music.
        muted: false,
        volume: 1.0,
        loopingType: BM_LOOPING_TYPE_ALL,
    },
    bmError: null,
};

export const PlayerContext = createContext(initialPlayerState);

export const PlayerProvider = ({ reducer, initialState, children }) => (
    <PlayerContext.Provider value={useReducer(reducer, initialState)}>{children}</PlayerContext.Provider>
);

export const usePlayer = () => useContext(PlayerContext);

export const usePlayerDispatch = () => {
    const [_, dispatch] = usePlayer();

    return dispatch;
}
