// details.js

import * as React from 'react';
import { View, StyleSheet, Dimensions } from 'react-native';
import { useHeaderHeight, HeaderTitle } from '@react-navigation/elements';
import { Surface, Title, IconButton, Caption, Button, Text } from 'react-native-paper';
import Animated from 'react-native-reanimated';
import { SceneMap } from 'react-native-tab-view';
import { HScrollView } from 'react-native-head-tab-view';
import { CollapsibleHeaderTabView } from 'react-native-tab-view-collapsible-header';
import { useQuery } from "@apollo/client";
import { useTranslation } from 'react-i18next';
import { ScreenLoadingIndicator } from '../comps/indicators';
import { Error, NotFound } from '../comps/error';

import { ShareButton } from '../comps/buttons';
import Like from '../comps/like';
import { Portal } from 'react-native-paper';
import { useIsDark } from '../hooks/use-theme';
import ScrollHeader from '../comps/details/scroll-header';
import useScrollAnim, {FROZE_TOP} from '../comps/animations/scroll';
import { GET_STORY_DETAILS } from '../api/graphql/story';
import flatten from '../utils/flatten';

const initialLayout = { width: Dimensions.get('window').width };

const StoryDetailRoute = () => (
  <HScrollView index={0}>
    <View style={[styles.scene, { backgroundColor: '#ff4081' }]} />
  </HScrollView>
);

const ChapterListRoute = () => (
  <HScrollView index={1}>
    <View style={[styles.scene, { backgroundColor: '#673ab7' }]} />
  </HScrollView>
);

function StoryTabView({ route, navigation }) {
  const { storyCode } = route.params;
  const { t } = useTranslation();
  const [isDark] = useIsDark();
  const headerHeight = useHeaderHeight();
  const [index, setIndex] = React.useState(0);
  const [routes] = React.useState([
    { key: 'detail', title: t('Details') },
    { key: 'chapters', title: t('Chapters') },
  ]);

  const { data, loading, error, refetch, } = useQuery(GET_STORY_DETAILS, {
    notifyOnNetworkStatusChange: true,
    variables: {
      code: storyCode || 'The_Da_Vinci_Code' // TODO: remove.
    }
  })

  const { titleTransStyle, scrollHeaderTransStyle, makeScrollTrans } = useScrollAnim();

  const tintColor = isDark ? 'white' : 'black';

  React.useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (<Animated.View style={titleTransStyle}>
        <HeaderTitle>
          {story?.title}
        </HeaderTitle>
      </Animated.View>),
      headerTintColor: tintColor,
      headerRight: ({ size, color }) => (
        <View style={styles.barItem}>
          <IconButton
            onPress={() => navigation.push('Search')}
            icon="search"
            iconColor={color}
            size={size}
          />
          <IconButton
            onPress={() => navigation.push('Channel')}
            icon="md-grid-outline"
            iconColor={color}
            size={size}
          />
        </View>
      ),
    });
  }, [titleTransStyle, navigation]);

  if (loading) return <ScreenLoadingIndicator />
  if (error) return <Error onRefresh={() => refetch()} />
  if (!data?.storyByCode?.data) {
    return <NotFound back={() => navigation.goBack()} />
  }

  const renderScene = SceneMap({
    detail: StoryDetailRoute,
    chapters: ChapterListRoute,
  });

  const story = flatten(data.storyByCode.data);

  return (
    <>
      <CollapsibleHeaderTabView
        headerHeight={headerHeight}
        renderScrollHeader={() => 
          <ScrollHeader scrollHeaderTransStyle={scrollHeaderTransStyle} 
                        story={story} 
                        tintColor={tintColor} />}
        navigationState={{ index, routes }}
        renderScene={renderScene}
        onIndexChange={setIndex}
        initialLayout={initialLayout}
        frozeTop={FROZE_TOP}
        makeScrollTrans={makeScrollTrans}
      />
      <Portal>
        <Surface elevation={1} style={styles.bottomBar}>
          <ShareButton />
          <Like liked={story.liked} storyId={story.id} />
          <Button style={styles.start}
            textColor='white'
            mode="contained" onPress={() => console.log('Start View')}>
            {t('Start')}
          </Button>
        </Surface>
      </Portal>
    </>
  );
}

const styles = StyleSheet.create({
  scene: {
    flex: 1,
  },
  header: {
    marginHorizontal: 10,
    flexDirection: 'row',
    gap: 20,
    height: 145,
  },
  title: {
    color: 'white'
  },
  cover: {
    width: 112,
    height: 145,
    borderRadius: 5,
  },
  meta: {
    flexDirection: 'column',
    gap: 10,
  },
  icons: {
    flexDirection: 'row',
    gap: 20,
  },
  barItem: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  bottomBar: {
    width: '100%',
    height: 64,
    position: 'fixed',
    bottom: 0,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    gap: 30,
    paddingLeft: 20,
    paddingRight: 20,
  },
  start: {
    flexGrow: 1,
    borderWidth: 1,
    borderRadius: 25,
  }
});

export default StoryTabView;
