import React, { useMemo } from 'react';
import { View, StyleSheet } from 'react-native';
import { Caption, Subheading, IconButton, Colors, useTheme } from 'react-native-paper';
import Icon from 'react-native-vector-icons/Ionicons';
import { fmtNum } from './utils/number';
import { useTranslation } from 'react-i18next';

export const ViewsIcon = ({ views }) => {
  const theme = useTheme();

  return (
    <View style={styles.compact}>
        <Icon size={10} color={theme.colors.secondary} name={'eye'}/>
        <Caption>{fmtNum(views)}</Caption>
    </View>
  );
};

export const LikesIcon = ({ likes }) => {
  const theme = useTheme();

  return (
    <View style={styles.compact}>
        <Icon size={10} color={theme.colors.secondary} name={'heart'}/>
        <Caption>{fmtNum(likes)}</Caption>
    </View>
  );
};

export const NumChapIcon = ({ num }) => {
    const theme = useTheme();
  
    return (
      <View style={styles.compact}>
          <Icon size={10} color={theme.colors.secondary} name={'list'}/>
          <Caption>{fmtNum(num)}</Caption>
      </View>
    );
  };

export const ShareIcon = () => {
  const theme = useTheme();
  const { t } = useTranslation();

  return (
    <View style={styles.vcompact}>
        <Icon size={30} color={theme.colors.secondary} name={'share-social-outline'}/>
        <Caption>{t('Share')}</Caption>
    </View>
  );
};

export const LikeIcon = ({liked}) => {
  const theme = useTheme();
  const { t } = useTranslation();

  return (
    <View style={[styles.vcompact, {minWidth: 50}]}>
        <Icon size={30} color={theme.colors.secondary} name={ liked ? 'heart': 'heart-outline'}/>
        <Caption>{t(liked ? 'UnLike': 'Like')}</Caption>
    </View>
  );
};

export const CoinIcon = ({ num }) => {
  const theme = useTheme();
  const { t } = useTranslation();
  return (
    <View style={styles.vcompact}>
      <View style={styles.compact}>
          <Icon size={10} color={theme.colors.secondary} name={'credit-card'}/>
          <Caption>{t('Coin')}</Caption>
      </View>
      <Subheading>{fmtNum(num)}</Subheading>
    </View>
  );
};

