export const MuteIcon = ({muted, volume}) => {
  const theme = useTheme();
  const v = volume || 1;
  const icon = muted ? 'volume-mute':
              (
                v >= 0.5 ? 'volume-high'
                              : 'volume-medium'       
              );  
  return (
    <Icon size={30} 
          color={theme.colors.secondary} 
          name={icon}/>
  );
};

const styles = StyleSheet.create({
    compact: {
        gap: 5,
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center'
    },
    vcompact: {
      gap: 0,
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center'
  }
});
