// home.js

import * as React from 'react';
import { View, Text, Button, StyleSheet, ScrollView } from 'react-native';
import { Divider, IconButton } from 'react-native-paper';
import StoriesCollections from '../comps/home/StoriesCollections'; 
import TopCarousel from '../comps/home/TopCarousel';
import FeatureLinks from '../comps/home/Features';
import { PreferSwitch } from '../comps/prefer/switch';

function HomeScreen({ route, navigation }) {
  const [count, setCount] = React.useState(0);

  React.useEffect(() => {
    navigation.setOptions({
      headerRight: ({ size, color }) => (
        <View style={styles.barItem}>
          <IconButton
            onPress={() => navigation.push('Test')}
            icon="home"
            iconColor={color}
            size={size}
          />
          {/* <PreferSwitch size={size}/> */}
          <IconButton
            onPress={() => navigation.push('Search')}
            icon="search"
            iconColor={color}
            size={size}
          />
          <IconButton
            onPress={() => navigation.push('Channel')}
            icon="md-grid-outline"
            iconColor={color}
            size={size}
          />
        </View>
      ),
    });
  }, [navigation]);

  return (
    <ScrollView style={styles.container}>
      {/* <TopCarousel navigation={navigation}/> */}
      {/* <FeatureLinks navigation={navigation}/> */}
      <StoriesCollections navigation={navigation}/>
      <Divider />
      {/* <StoriesCollections /> */}
    </ScrollView>
  );
}

const styles = StyleSheet.create({
  container: {
    //flexDirection: 'row',
  },
  barItem: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
});

export default HomeScreen;
