import { gql } from "@apollo/client";
export const GET_CATEGORIES = gql`
	query AllCategories($sort: [String]) {
		categories(sort: $sort) {
			data {
				id
				attributes {
					code
					name
					headline
					description
					pickup
					image {
					  data {
						id
						attributes {
						  url
						}
					  }
					}
				}
			}
		}
	}
`;
