// channel.js

import * as React from 'react';
import { useWindowDimensions, StyleSheet } from 'react-native';
import { useTheme } from 'react-native-paper';
import { useQuery } from "@apollo/client";
import { useTranslation } from 'react-i18next';
import { GET_CATEGORIES } from '../api/graphql/category';
import flatten from '../utils/flatten';
import { Error } from '../comps/error';
import {
  ScreenLoadingIndicator,
  LazyLoadingIndicator
} from '../comps/indicators';

import ListStories from '../comps/channels/ListStories';

import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { useIsFemale } from '../hooks/use-prefer';

const Tab = createMaterialTopTabNavigator();

function ChanelScreen() {
  const layout = useWindowDimensions();
  const insets = useSafeAreaInsets();
  const theme = useTheme();
  const { t } = useTranslation();
  const isFemale = useIsFemale();

  const { data, loading, error, refetch, } = useQuery(GET_CATEGORIES, {
    notifyOnNetworkStatusChange: true,
    variables: {
      sort: isFemale ? 'order:DESC': 'order:ASC'
    }
  })

  if (loading) return <ScreenLoadingIndicator />
  if (error) return <Error onRefresh={() => refetch()} />

  const categories = flatten(data.categories.data);

  return (
    <Tab.Navigator
      initialLayout={{ with: layout.width }}
      sceneContainerStyle={{
        paddingTop: insets.top,
        paddingBottom: insets.bottom,
        paddingLeft: insets.left,
        paddingRight: insets.right,
      }}
      screenOptions={{
        tabBarActiveTintColor: theme.colors.secondary,
        tabBarLabelStyle: { fontSize: 10, textTransform: 'none' },
        tabBarItemStyle: { width: 100 },
        tabBarStyle: { backgroundColor: theme.colors.surface },
        lazyPreloadDistance: 1,
        lazyPlaceholder: ()=> <LazyLoadingIndicator msg={t('Loading..')}/>,
        lazy: true,
        animationEnabled: true,
      }}
    >
      {categories.map(({ code, name }) => (
        <Tab.Screen
          key={code}
          name={code}
          options={{
            title: name,
          }}
          component={ListStories}
        />
      ))}
    </Tab.Navigator>
  );
}

const styles = StyleSheet.create({});

export default ChanelScreen;
