import { gql } from "@apollo/client";

const GET_TODOS = gql`
    query getTodos($start: Int, $limit: Int) {
        todos(pagination: {start: $start, limit:  $limit }) {
            data {
                id
                attributes {
                    title
                    hours
                }
            }
            meta {
                pagination {
                    total
                    page
                    pageSize
                    pageCount
                }
            }
        }
    }
`

const SET_HOURS_TODO = gql`
    mutation setTodoHours($id: ID!, $hours: Int!) {
        updateTodo(id: $id, data: {hours: $hours}) {
        data {
            id,
            attributes {
            hours
            }
        }
        }
    }
`;

const ADD_TODO = gql`
    mutation addTodo($hours: Int!, $title: String!) {
        createTodo(data: {hours: $hours, title: $title}) {
        data {
            id
            attributes {
            hours
            title
            }
        }
        }
    }
`;

export { GET_TODOS, ADD_TODO, SET_HOURS_TODO }