import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { useIsFocused } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { useIsDark } from '../hooks/use-theme';
import { Image, View, StyleSheet } from 'react-native';
import {
  useTheme,
  Portal,
  FAB,
  IconButton,
  MD3Colors,
} from 'react-native-paper';
import Icon from 'react-native-vector-icons/Ionicons';

import { BadgeIcon } from '../comps/buttons';
import ChanelScreen from '../screens/channel';
import ChartScreen from '../screens/chart';
import ConfirmModal from '../screens/confirm';
import CreatePostScreen from '../screens/create';
import DetailsScreen from '../screens/details';
import ExplorerScreen from '../screens/explorer';
import HomeScreen from '../screens/home';
import InboxScreen from '../screens/inbox';
import LibraryScreen from '../screens/library';
import MeScreen from '../screens/me';
import NotFoundScreen from '../screens/notfound';
import ReadingScreen from '../screens/reading';
import SearchScreen from '../screens/search';
import SettingScreen from '../screens/settings';
import TestScreen from '../screens/test';
import { PreferAnim } from '../comps/prefer/anim';
import ScreenIds from '../constants/screens';

const Stack = createNativeStackNavigator();
const Tab = createBottomTabNavigator();

const initialRouteName = ScreenIds.Home;

function LogoTitle() {
  return (
    <Image
      style={{ width: 50, height: 50 }}
      source={require('../assets/icon.png')}
    />
  );
}

function LibraryStack({ navigation }) {
  const { t } = useTranslation();

  console.log("Library Stack Rendering..");
  
  return (
    <Stack.Navigator>
      <Stack.Screen
        name={ScreenIds.Me}
        component={MeScreen}
        options={{
          title: t('Me'),
          headerRight: ({ size, color }) => (
            <View style={styles.barItem}>
              <BadgeIcon
                icon="mail-outline"
                badges={3}
                onPress={() => navigation.push(ScreenIds.Inboxes)}
                iconColor={color}
                size={size}
              />
              <IconButton
                onPress={() => navigation.push(ScreenIds.Settings)}
                icon="md-settings-outline"
                iconColor={color}
                size={size}
              />
            </View>
          ),
        }}
      />
      <Stack.Screen
        name={ScreenIds.Settings}
        component={SettingScreen}
        options={{
          title: t('Settings'),
        }}
      />
      <Stack.Screen
        name={ScreenIds.Inboxes}
        component={InboxScreen}
        options={{
          title: t('Inboxes'),
        }}
      />
    </Stack.Navigator>
  );
}

/** story main stack: home -> details -> reading.  */
function HomeStack({ navigation }) {
  const { t } = useTranslation();
  const isFocused = useIsFocused();

  return (
    <>
      <Stack.Navigator>
        <Stack.Screen
          name={ScreenIds.Home}
          component={HomeScreen}
          options={{
            title: t('Home'),
            headerRight: ({ size, color }) => (
              <View style={styles.barItem}>
                <IconButton
                  onPress={() => {}}
                  icon="search"
                  iconColor={color}
                  size={size}
                />
                <IconButton
                  onPress={() => {}}
                  icon="md-grid-outline"
                  iconColor={color}
                  size={size}
                />
              </View>
            ),
          }}
        />
        <Stack.Screen
          name={ScreenIds.Channel}
          component={ChanelScreen}
          options={{
            title: t('Channel'),
          }}
        />
      </Stack.Navigator>

      <Portal>
        <FAB
          visible={isFocused} // show FAB only when this screen is focused
          icon="play-circle"
          style={{
            position: 'absolute',
            bottom: 100,
            right: 16,
          }}
        />
        {/* <PreferAnim /> */}
      </Portal>
    </>
  );
}

function AppTabs({ navigation }) {
  const { t } = useTranslation();

  return (
    <Tab.Navigator>
      <Tab.Screen
        name="HomeStack"
        component={HomeStack}
        options={{
          headerShown: false,
          tabBarLabel: t('Home'),
          tabBarIcon: ({ color, size }) => (
            <Icon name="home" size={size} color={color} />
          ),
        }}
      />
      <Tab.Screen
        name={ScreenIds.Chart}
        component={ChartScreen}
        options={{
          title: t('#DHChart'),
          tabBarLabel: t('#DHChart'),
          tabBarIcon: ({ color, size }) => (
            <Icon name="stats-chart" size={size} color={color} />
          ),
        }}
      />
      <Tab.Screen
        name={ScreenIds.Explorer}
        component={ExplorerScreen}
        options={{
          title: t('Explorer'),
          tabBarLabel: t('Explorer'),
          tabBarIcon: ({ color, size }) => (
            <Icon name="compass-outline" size={size} color={color} />
          ),
          headerRight: ({ size, color }) => (
            <IconButton
              onPress={() => navigation.push(ScreenIds.Search)}
              icon="search"
              iconColor={color}
              size={size}
            />
          ),
        }}
      />
      <Tab.Screen
        name="LibraryStack"
        component={LibraryStack}
        options={{
          headerShown: false,
          tabBarLabel: t('Library'),
          tabBarBadge: null,
          tabBarBadgeStyle: styles.barBadge,
          tabBarIcon: ({ color, size }) => (
            <Icon name="person-circle" size={size} color={color} />
          ),
        }}
      />
    </Tab.Navigator>
  );
}

function AppStack() {
  const { t } = useTranslation();
  const [isDark] = useIsDark();

  return (
    <Stack.Navigator
      initialRouteName={initialRouteName}
    >
      <Stack.Screen
        name="AppTabs"
        component={AppTabs}
        options={{ headerShown: false }}
      />
      <Stack.Screen name={ScreenIds.Search} component={SearchScreen} />

      <Stack.Screen
          name={ScreenIds.Details}
          component={DetailsScreen}
          initialParams={ {storyCode: null} }
          options={{
            title: null,
            headerTransparent: true,
            headerTintColor: isDark ? 'white': 'black',
          }}
        />
        <Stack.Screen
          name={ScreenIds.Reading}
          component={ReadingScreen}
          options={{
            title: t('Reading'),
          }}
        />

      <Stack.Screen name="Create" component={CreatePostScreen} />

      <Stack.Screen
        name="ConfirmModal"
        component={ConfirmModal}
        options={{
          presentation:
            'transparentModal' /** use presentation modal to create modal */,
          headerShown: false,
          cardOverlayEnabled: true,
        }}
      />
      <Stack.Screen name={ScreenIds.NotFound} component={NotFoundScreen} />
      <Stack.Screen name={ScreenIds.Test} component={TestScreen} />
    </Stack.Navigator>
  );
}

const styles = StyleSheet.create({
  barItem: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  barBadge: {
    minWidth: 12,
    maxWidth: 12,
    maxHeight: 12,
    fontSize: 6,
    lineHeight: 8,
    alignSelf: undefined,
  },
});

export default AppStack;
