import { useState, useEffect, useRef } from "react";
import { usePlayer } from "../contexs/player";
import {
    Audio,
    InterruptionModeAndroid,
    InterruptionModeIOS,
} from "expo-av";
import Hls from 'hls.js';
import { SYS_SOUD_URIs } from "../constants/player";

const AudioError = {
    PLAYLIST_EMPTY: 'PLAY LIST EMPTY',
    INVALID_INDEX: 'INVALID PLAY INDEX',
    INIT_ERROR: 'INIT_ERROR',
    AUDIO_ERROR: 'AUDIO PLAYBACK ERROR',
    WAIT_INTERACT: 'WAIT_INTERACT',
}

/** create HTML5 Audio instance and return ref */
function createAudioRef(onStatusUpdate) {
    const audio = new window.Audio();            // create audio wo/ src
    audio.preload = 'auto';                      // intend to play through
    audio.autoplay = true;                       // autoplay when loaded
    audio.oncanplay = () => {
        onStatusUpdate({
            isLoaded: true,
        })
    }
    audio.onplay = () => {
        onStatusUpdate({
            isPlaying: true,
        })
    };
    audio.onended = () => {
        onStatusUpdate({
            didJustFinish: true,
        })
    };
    audio.onpause = () => {
        onStatusUpdate({
            isPlaying: false,
        })
    };
    audio.ontimeupdate = () => {
        onStatusUpdate({
            positionMillis: audio.currentTime * 1000,
        })
    };
    return audio;
}

/** update session metadata for screen-lock control | web */
async function updateMeta(track) {
    if ("mediaSession" in navigator) {
        navigator.mediaSession.metadata = new MediaMetadata({
            title: track.title,
            artist: track.artist,
            album: track.album,
            artwork: [{ src: track.artwork, },],
            // 512x512 or 256x256
        });
    }
}

// navigator.mediaSession.setPositionState: is need ?


/** register screen-lock action handlers | web */
async function setActionHandlers(actions, reset=false) {
    if ( !("mediaSession" in navigator) ) {
        return;
    }

    const actionHandlers = [
        ['play',          () => { actions.play() }],
        ['pause',         () => { actions.pause() }],
        ['stop',          () => { actions.stop() }],
        ['seekbackward',  (details) => { actions.seek(details.seekOffset || -10) }],
        ['seekforward',   (details) => { actions.seek(details.seekOffset || 30) }],
        ['seekto',        (details) => { actions.seek(details.seekTime) }],
      ];

    for (const [action, handler] of actionHandlers) {
        try {
            navigator.mediaSession.setActionHandler(action, reset ? null : handler);
        } catch (error) {
            console.log(`The media session action "${action}" is not supported yet.`);
        }
    }
}

async function createAvRef(autoPlay, track, stat, onStatusUpdate) {
    const source = { uri: track.url };
    const initialStatus = {
        ...stat,
        shouldPlay: autoPlay,
        isLooping: false,
    };

    const { sound, status } = await Audio.Sound.createAsync(
        source,
        initialStatus,
        onStatusUpdate,
    );
    return sound;
}

/** create HLS ref and attach to audioRef */
async function createHlsRef(autoPlay, track, audioRef, onStatusUpdate) {
    console.log("init hls");
    if (!audioRef.current) {
        return {
            err: {
                code: AudioError.INIT_ERROR,
                msg: 'hls audio ref not initialized.',
            }
        }
    }

    // -- fallback use default system audio tag.
    if (!Hls.isSupported()) {
        audioRef.current.src = track.url;
        audioRef.current.load();
        return { hls: null, error: null };
    }

    const hls = new Hls();
    hls.attachMedia(audioRef.current);
    hls.on(Hls.Events.MEDIA_ATTACHED, () => {

        hls.loadSource(track.url);

        hls.on(Hls.Events.MANIFEST_PARSED, () => {
            hls.on(Hls.Events.LEVEL_LOADED, (_, data) => {
                const durationSeconds = data.details.totalduration;
                onStatusUpdate({
                    isLoaded: true,
                    durationMillis: durationSeconds * 1000,
                    positionMillis: 0,
                    shouldPlay: autoPlay,
                });

                if (autoPlay) {
                    audioRef.current?.play()
                        .catch(() => {
                            const msg = {
                                code: AudioError.WAIT_INTERACT,
                                msg: 'Unable to autoplay prior to user interaction with the dom.'
                            }
                            onStatusUpdate({
                                error: msg,
                            });
                        });
                }
            });
        });
    });

    hls.on(Hls.Events.ERROR, function (event, data) {
        if (data.fatal) {
            switch (data.type) {
                case Hls.ErrorTypes.NETWORK_ERROR:
                    hls.startLoad();
                    break;
                case Hls.ErrorTypes.MEDIA_ERROR:
                    hls.recoverMediaError();
                    break;
                default:
                    const msg = {
                        code: AudioError.AUDIO_ERROR,
                        msg: 'Unable to play hls audio, ' + data
                    }
                    onStatusUpdate({
                        error: msg
                    })
                    break;
            }
        }
    });

    return { hls }
}

export const usePlayback = () => {
    const audioRef = useRef(null)
    const hlsRef = useRef(null)
    const hlsFallback = useRef(false)
    const avRef = useRef(null)
    const [state, dispatch] = usePlayer();
    const { tracks,
        track,
        activeIndex,
        playbackStat,
        playbackError } = state;

    console.log("activeIndex: ", activeIndex,
        " tracks: ", tracks,
        "track: ", track);

    function onPlaybackStatusUpdate(status) {
        if (status.error) {
            return error({ code: AudioError.AUDIO_ERROR, msg: `FATAL PLAYER ERROR: ${status.error}` })
        }

        if (status.didJustFinish) {
            skipNext();
        }
        dispatch({
            type: 'setPlaybackStat',
            payload: status
        })
    }

    function onError(err) {
        dispatch({
            type: 'setPlaybackError',
            payload: err
        })
    }

    function error(msg) {
        console.log(msg);
        onError(msg);
    }

    async function stop() {
        if (hlsRef.current) {
            hlsRef.current.destroy();
        }
        if (avRef.current) {
            await avRef.current.unloadAsync();
        }
        if (hlsFallback.current && audioRef.current) {
            audioRef.current.pause();
        }

        /** clear action handlers */
        await setActionHandlers(null, true);
    }

    async function initPlayback() {
        console.log('initPlayback')
        await stop();
        if (!track.isLoaded) { return; }

        if (track.hls) {
            const { err, hls } = await createHlsRef(
                true,
                track,
                audioRef,
                onPlaybackStatusUpdate
            );
            if (err) {
                return error(err);
            }
            if (hls) {
                hlsRef.current = hls;
            }
            else {
                hlsFallback.current = true;
            }
        }
        else {
            avRef.current = await createAvRef(
                true,
                track,
                playbackStat,
                onPlaybackStatusUpdate
            );
        }

        /** update session meta screen-lock controls */
        await updateMeta(track);
        
        /** register action handlers screen-lock controls */
        await setActionHandlers({
            play, pause, stop, seek
        }, true);
    }

    useEffect(() => {
        if (!audioRef.current) {
            audioRef.current = createAudioRef(onPlaybackStatusUpdate)
        }

        return () => {
            if (audioRef.current) {
                audioRef.current.pause();
                audioRef.current = null;
            }
        }
    }, []);

    useEffect(() => {
        if (track.isLoaded) {
            initPlayback();
        }

        return () => {
            stop();
        }
    }, [track])

    async function play(index) {
        /** ensure playlist setup first! */
        if (!tracks || tracks.length === 0) {
            return error(
                {
                    code: AudioError.PLAYLIST_EMPTY,
                    msg: `FATAL MB-PLAYER PLAYLIST NOT SETUP YET`
                }
            )
        }

        /** only create instance if current playback not setup or index changed. */
        if (typeof index === 'number' && index != activeIndex) {
            if (index < 0 || index > tracks.length - 1) {
                return error({
                    code: AudioError.INVALID_INDEX,
                    msg: 'FATAL MB-PLAYER TRY PLAY INVALID INDEX'
                });
            }

            const data = tracks[index];
            return dispatch({
                type: 'playTrack',
                payload: {
                    index: index,
                    track: data
                }
            });
        }

        // fallback to case: play first track
        if (typeof index !== 'number' && !track.isLoaded) {
            const data = tracks[0];
            return dispatch({
                type: 'playTrack',
                payload: {
                    index: 0,
                    track: data
                }
            });
        }

        // track ready => wait start.
        if (track.isLoaded) {
            await resume();
        }
    }

    async function resume() {
        if (!track.isLoaded) return;
        if (track.hls) {
            audioRef.current?.play();
        }
        else {
            await avRef.current.playAsync();
        }
    }

    async function pause() {
        if (!track.isLoaded) return;
        if (track.hls) {
            audioRef.current?.pause();
        }
        else {
            await avRef.current.pauseAsync();
        }
    }

    // skip to next/previous track
    async function skipNext(direction) {
        let idx = 0;
        if (direction === 'prev') {
            idx = activeIndex - 1;
            if (idx < 0) {
                idx = tracks.length - 1;
            }
        } else {
            idx = activeIndex + 1;
            if (idx >= tracks.length) {
                idx = 0;
            }
        }

        await play(idx);
    }

    async function setVolume(val) {
        if (!track.isLoaded) return;

        const v = val < 0 ? 0 : (val > 1 ? 1 : val);

        if (!track.hls) {
            return await avRef.current.setVolumeAsync(v);
        }

        audioRef.current.volume = v;

        onPlaybackStatusUpdate({
            volume: v
        });
    }

    async function setMute(muted) {
        if (!track.isLoaded) return;

        if (!track.hls) {
            return await avRef.current.mute(muted);
        }

        audioRef.current.muted = muted;

        onPlaybackStatusUpdate({
            muted: muted
        });
    }

    async function seek(ms) {
        
    }

    /*
    // Seek to a new position in the currently playing track.
    // (per Percentage through the song to skip.)
    async function seek(per) {
        const to = playbackStat.durationMillis * per;
        await jump(to);
    }

    // jump x-ms
    async function jump(ms) {
        if (!track.hls) {
            await avRef.current.playFromPositionAsync(ms);
        }
        else {
            audioRef.current?.currentTime = ms / 1000;
            onPlaybackStatusUpdate({
                positionMillis: ms
            });
        }
    }
    */

    return {
        play,
        pause,
        resume,
        skipNext,
        // jump,
        // seek,
        setVolume,
        setMute,
        stat: playbackStat,
        err: playbackError,
    }
}