import { useState, useEffect, useRef } from "react";
import { usePlayer } from "../contexs/player";
import { BM_URIs } from "../constants/player";
import BmPlayer from "../comps/players/playbacks/bm";

export const useBGMusic = () => {
    const bmPlayer = useRef(null)
    const [state, dispatch] = usePlayer();
    const { bmState, bmError } = state;

    function onStateChanged(status, isProgressUpdated) {
        /** only dispatch if change other state than position. */
        if (!isProgressUpdated) {
            dispatch({
                type: 'setBMState',
                payload: status
            })
        }
    }

    function onError(err) {
        dispatch({
            type: 'setBMError',
            payload: err
        })
    }

    useEffect(() => {
        bmPlayer.current = new BmPlayer(BM_URIs, bmState, onError, onStateChanged);
        return () => {
            bmPlayer.current.destroy()
            bmPlayer.current = null
        }
    }, [])

    const play = async () => {
        await bmPlayer.current.play()
    }

    const pause = async () => {
        await bmPlayer.current.pause()
    }

    const skipNext = async () => {
        await bmPlayer.current.skip();
    }

    const setVolume = async (val) => {
        const v = val < 0 ? 0: (val > 1 ? 1 : val);
        await bmPlayer.current.volume(v);
    }

    const setMute = async(muted) => {
        await bmPlayer.current.mute(muted);
    }

    return { bmPlayer, bmState, bmError, play, pause, skipNext, setVolume, setMute}
}