// https://docs.expo.dev/guides/facebook-authentication/
import { useEffect, useState } from 'react';
import * as AuthSession from 'expo-auth-session';
import * as Facebook from 'expo-auth-session/providers/facebook';
import * as WebBrowser from 'expo-web-browser';

import { Image, StyleSheet, View } from 'react-native';
import { Button, Text } from 'react-native-paper';

/** Invoke this method to the app screen you want to redirect back to by adding the following code snippet
 * If this method is not invoked, the popup will not dismiss itself
 */
WebBrowser.maybeCompleteAuthSession();

function FacebookAuth() {
	const [ user, setUser ] = useState(null);
	const [ request, response, promptAsync ] = Facebook.useAuthRequest({
		clientId: '1715434385496424' // TODO: move to const file.
	});

	if (request) {
		console.log(
			'You need to add this url to your authorized redirect urls on your Facebook app: ' + request.redirectUri
		);
	}

	useEffect(
		() => {
			if (response && response.type === 'success' && response.authentication) {
				(async () => {
					const userInfoResponse = await fetch(
						`https://graph.facebook.com/me?access_token=${response.authentication
							.accessToken}&fields=id,name,picture.type(large)`
					);
					const userInfo = await userInfoResponse.json();
					setUser(userInfo);
				})();
			}
		},
		[ response ]
	);

	const handlePressAsync = async () => {
		const result = await promptAsync();
		if (result.type !== 'success') {
			alert('Uh oh, something went wrong');
			return;
		}
	};

	return (
		<View style={styles.container}>
			{user ? (
				<Profile user={user} />
			) : (
				<Button disabled={!request} onPress={handlePressAsync}>
					Sign in with Facebook
				</Button>
			)}
		</View>
	);
}

function Profile({ user }) {
	return (
		<View style={styles.profile}>
			<Image source={{ uri: user.picture.data.url }} style={styles.image} />
			<Text style={styles.name}>{user.name}</Text>
			<Text>ID: {user.id}</Text>
		</View>
	);
}



const styles = StyleSheet.create({
	container: {
		flex: 1,
		alignItems: 'center',
		justifyContent: 'center'
	},
	profile: {
		alignItems: 'center'
	},
	name: {
		fontSize: 20
	},
	image: {
		width: 100,
		height: 100,
		borderRadius: 50
	}
});


export default FacebookAuth;