// inbox.js

import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { View, FlatList, StyleSheet, RefreshControl } from 'react-native';
import {
  Text,
  List,
  Caption,
  Button,
  useTheme,
  ActivityIndicator,
} from 'react-native-paper';

import { queryBy } from '../api';
import InboxItem from '../comps/inbox/item';
import { ListLoadmoreIndicator } from '../comps/indicators';
import { ListHeader, ItemSeparator, ListEmpty } from '../comps/list';

function InboxScreen({ navigation }) {
  const { t } = useTranslation();
  const theme = useTheme();

  const [loading, setLoading] = React.useState(false);
  const [refreshing, setRefreshing] = React.useState(false);
  const [moreLoading, setMoreLoading] = React.useState(false);

  const [data, setData] = React.useState([]);
  const [totalPage, setTotalPage] = React.useState(1);
  const [page, setPage] = React.useState(1);

  const onRefresh = React.useCallback(async () => {
    setRefreshing(true);

    setTimeout(() => {
      setRefreshing(false);
      // todo
    }, 1000);
  }, [refreshing]);

  const fetchMore = React.useCallback(async () => {
    console.log('receive event fetchMore: ', page, totalPage);
    if (!moreLoading && page < totalPage) {
      setPage((prev) => prev + 1);
      console.log('page -> ', page);
    }
  });

  const requestAPI = React.useCallback(async () => {
    if (page == 1) {
      setLoading(true);
    } else {
      setMoreLoading(true);
    }

    const ret = await queryBy(page);
    console.log('request api ret: ', ret);
    if (page == 1) {
      setLoading(false);
    } else {
      setMoreLoading(false);
    }
    setData([...data, ...ret.items]);
    setTotalPage(ret.totalPage);
  }, [page]);

  React.useEffect(() => {
    requestAPI();
    console.log('fetch api for page: ', page, 'in: ', totalPage);
  }, [page]);

  const renderFooter = React.memo((props) => (
    <View style={styles.footer}>
      {moreLoading && <ListLoadmoreIndicator />}
      {page === totalPage && <Caption>No more items at the moment</Caption>}
    </View>
  ));

  return (
    <>
      {loading ? (
        <View style={styles.loading}>
          <ActivityIndicator size="large" />
        </View>
      ) : (
        <FlatList
          contentContainerStyle={styles.list}
          data={data}
          ListHeaderComponent={ListHeader}
          ListFooterComponent={renderFooter}
          ListEmptyComponent={() => <ListEmpty onRefresh={onRefresh} />}
          ItemSeparatorComponent={ItemSeparator}
          renderItem={({ item }) => (
            <InboxItem
              title={item.title}
              desc={item.desc}
              updated={item.updated}
            />
          )}
          keyExtractor={(item) => item.id}
          onEndReachedThreshold={0.2}
          onEndReached={fetchMore}
          refreshControl={
            <RefreshControl
              refreshing={refreshing}
              onRefresh={onRefresh}
              title={t('loading inboxes')}
              titleColor={theme.colors.text}
              colors={theme.colors.primary}
              tintColor={theme.colors.primary}
            />
          }
        />
      )}
    </>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    gap: 10,
    borderColor: 'red',
    alignItems: 'center',
    justifyContent: 'center',
  },
  empty: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  footer: {
    flex: 1,
    paddingBottom: 10,
    alignItems: 'center',
    justifyContent: 'center',
  },
  loading: {
    flex: 1,
    flexGrow: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  list: {
    flexGrow: 1,
  },
  item: {
    flex: 1,
    padding: 20,
    fontSize: 15,
    marginTop: 5,
  },
});

export default InboxScreen;
