const FORMAT_VI = 'Vietnamese';
const FORMAT_EN = 'English';

function convertDate(format: string, date: Date): string {
    const pad = (s) => (s < 10 ? '0' + s : s);
    switch (format) {
      default:
      case FORMAT_VI:
        return [
          pad(date.getDate()),
          pad(date.getMonth() + 1),
          date.getFullYear(),
        ].join('-');
      case FORMAT_EN:
        return [
          date.getFullYear(),
          pad(date.getMonth() + 1),
          pad(date.getDate()),
        ].join('-');
    }
  }

const DateUtils = {
    toVietNamese: (date: Date): string => convertDate(FORMAT_VI, date),
    toEnglish: (date: Date): string => convertDate(FORMAT_EN, date),
    toStr: (date: Date): string => convertDate(FORMAT_EN, date),
};
  
export default DateUtils;