import {
    Audio,
    InterruptionModeAndroid,
    InterruptionModeIOS,
} from "expo-av";

import { BM_LOOPING_TYPE_ALL, 
         BM_LOOPING_TYPE_ONE } from "../../../constants/player";

const initState = {
    volume: 1.0,
    muted: false,
    loopingType: BM_LOOPING_TYPE_ALL,
};

const BmError = {
    PLAYLIST_EMPTY: 'PLAY LIST EMPTY',
    INVALID_INDEX: 'INVALID PLAY INDEX',
    AUDIO_ERROR: 'AUDIO PLAYBACK ERROR',
}

class BmPlayer {
    constructor(initUrls = [], state,  onError, onStateChanged) {
        this.playlist = [];
        this.index = 0;
        this.playback = null;
        this.initState = state || initState;
        this.state = this.initState;

        // generate playlist from list of urls.
        for (let i = 0; i < initUrls.length; i++) {
            this.playlist.push({
                url: initUrls[i]
            })
        }

        this.onError = onError;
        this.onStateChanged = onStateChanged;
        this.init();
    }

    init() {
        Audio.setAudioModeAsync({
            allowsRecordingIOS: false,
            staysActiveInBackground: true,
            interruptionModeIOS: InterruptionModeIOS.DoNotMix,
            playsInSilentModeIOS: true,
            shouldDuckAndroid: true,
            interruptionModeAndroid: InterruptionModeAndroid.DoNotMix,
            playThroughEarpieceAndroid: false
        });
    }

    // cleanup
    async destroy() {
        await this.stop();
        this.state = this.initState;
        this.playlist = [];
        this.onError = null;
        this.onStateChanged = null;
    }

    async stop() {
        /** ensure clean up memory  */
        if (this.playback != null) {
            await this.playback.unloadAsync();
            this.playback = null;
        }
    }

    async instancePlayback(uri, playing) {
        await this.stop();

        const source = { uri: uri };
        const initialStatus = {
            ...this.state,
            shouldPlay: playing,
            isLooping: this.state.loopingType === BM_LOOPING_TYPE_ONE
        };
         
        const { sound, status } = await Audio.Sound.createAsync(
            source,
            initialStatus,
            this.onPlaybackStatusUpdate,
        );

        this.playback = sound;
        return this.playback;
    }

    onPlaybackStatusUpdate = status => {
        if (status.isLoaded) {
            const isProgressUpdated = Object
                .keys(status)
                .filter(k => k !== 'positionMillis')
                .every(k => status[k] === this.state[k]);

            this.state = { ...status };
            if (this.onStateChanged) this.onStateChanged(this.state, isProgressUpdated);
            
            if (status.didJustFinish && !status.isLooping) {
                this.skip();
            }
        } else {
            if (status.error) {
                return this.error({ code: BmError.AUDIO_ERROR, msg: `FATAL PLAYER ERROR: ${status.error}`})
            }
        }
    };

    // add list urls to end of playlist.
    addUrls(urlsToAdd) {
        for (let i = 0; i < urlsToAdd.length; i++) {
            this.playlist.push({
                url: urlsToAdd[i]
            });
        }
    }

    error(msg) {
        if (this.onError) this.onError(error);
        console.log(error);
    }

    async play(index) {
        /** ensure playlist setup first! */
        if (this.playlist.length === 0) {
            return this.error(
                { code: BmError.PLAYLIST_EMPTY, msg: `FATAL MB-PLAYER PLAYLIST NOT SETUP YET`}
            ) 
        }

        /** only create instance if current playback not setup or index changed. */
        if (typeof index === 'number' && index != this.index) {
            if (index < 0 || index > this.playlist.length - 1) {
                return this.error({code: BmError.INVALID_INDEX, msg: 'FATAL MB-PLAYER TRY PLAY INVALID INDEX'});
            }
            this.index = index;
            const data = this.playlist[this.index];
            return await this.instancePlayback(data.url, true);
        }

        // fallback to case force-play first
        if (typeof index !== 'number' && !this.playback) {
            this.index = 0;
            return await this.instancePlayback(this.playlist[0].url, true);
        }
        if (this.playback) {
            await this.playback.playAsync();
        }
    }

    // pause current track
    async pause() {
        if (this.playback) await this.playback.pauseAsync();
    }

    // skip to next/previous track
    async skip(direction) {
        let idx = 0;
        if (direction === 'prev') {
            idx = this.index - 1;
            if (idx < 0) {
                idx = this.playlist.length - 1;
            }
        } else {
            idx = this.index + 1;
            if (idx >= this.playlist.length) {
                idx = 0;
            }
        }

        await this.skipTo(idx);
    }

    // skip to specific track index in playlist
    async skipTo(idx) {
        // play new track.
        await this.play(idx)
    }

    // Set the volume and update the volume slider display.
    async volume(val) {
        if (this.playback) {
            await this.playback.setVolumeAsync(val);
        }
    }

    async mute(muted) {
        if (this.playback) {
            await this.playback.setIsMutedAsync(muted)
        }
    }

    // get state playing ?
    async playing() {
        if (this.playback) {
            const status = await this.playback.getStatusAsync();
            return status.isPlaying;
        }
    }

    // Seek to a new position in the currently playing track.
    // (per Percentage through the song to skip.)
    async seek(per) {
        if (this.playback) {
            const to = this.state.duration * per;
            await this.playback.playFromPositionAsync(to);
        }
    }

    // jump x-ms
    async jump(ms) {
        if (this.playback) {
            await this.playback.playFromPositionAsync(ms);
        }
    }
}

export default BmPlayer;