import { useState } from "react";
import { Dimensions } from 'react-native';
import Animated, {
    useSharedValue,
    useAnimatedStyle,
    useDerivedValue,
    interpolateColor,
    interpolate, Extrapolate,
  } from 'react-native-reanimated';

const G_WIN_WIDTH = Dimensions.get('window').width
export const SCROLL_HEAD_HEIGHT = 230
export const FROZE_TOP = 64
const moveDistance = SCROLL_HEAD_HEIGHT - FROZE_TOP;

export default function useScrollAnim() {
    const [scrollTrans, setScrollTrans] = useState(useSharedValue(0));

    const transXValue = useDerivedValue(() => {
      const left = G_WIN_WIDTH / 2;
      return interpolate(scrollTrans.value,
        [0, moveDistance],
        [0, -left],
        Extrapolate.CLAMP
      )
    });
  
    const transYValue = useDerivedValue(() => {
      return interpolate(scrollTrans.value,
        [0, moveDistance],
        [0, FROZE_TOP],
        Extrapolate.CLAMP
      )
    });
  
    const scaleValue = useDerivedValue(() => {
      return interpolate(scrollTrans.value,
        [0, moveDistance],
        [1, 0.7],
        Extrapolate.CLAMP)
    })

    const headerOpacityValue = useDerivedValue(() => {
      return interpolate(scrollTrans.value,
        [0,  moveDistance * 0.7, moveDistance * 0.9, moveDistance],
        [1, 0.8, 0, 0],
        Extrapolate.CLAMP);
    });
  
    /** animated style for scroll headers. */
    const scrollHeaderTransStyle = useAnimatedStyle(() => {
      return {
        transform: [
          {
            translateX: transXValue.value
          },
          {
            translateY: transYValue.value
          },
          {
            scale: scaleValue.value
          }
        ],
        opacity: headerOpacityValue.value,
      }
    });
  
    /** animated style for top-bar headers. */
    const titleOpacity = useDerivedValue(() => {
      return interpolate(scrollTrans.value,
        [0, moveDistance * 0.8, moveDistance],
        [0, 0.0, 1],
        Extrapolate.CLAMP);
    });
  
    const titleTransStyle = useAnimatedStyle(() => {
      return {
        opacity: titleOpacity.value,
      }
    });
  
    const makeScrollTrans = (st) => {
      setScrollTrans(st);
    };
  
    return {
        makeScrollTrans, titleTransStyle, scrollHeaderTransStyle
    }
}