import { gql } from "@apollo/client";

const GET_TELEMETRY = gql`
    query getTelemetry {
        telemetry {
        data {
            id
            attributes {
            requests
            }
        }
        }
    }
`

const INCREMENT_TELEMETRY = gql`
  # Increments a back-end counter and gets its resulting value
  mutation increTelemetry {
    increTeletry {
      data {
        id
        attributes {
          requests
        }
      }
    }
  }
`;

export { GET_TELEMETRY, INCREMENT_TELEMETRY }