import { 
    MEDIA_HOST,
    STORY_DEFAULT_THUMBNAIL, } from "../constants";

function coverUri(cover, extUrl) {
    if (cover.url) {
        if (cover.url.startsWith("http")){
            return cover.url;
        }
        return `${MEDIA_HOST}${cover.url}`;
    }

    if (extUrl) return extUrl;

    return `${MEDIA_HOST}/${STORY_DEFAULT_THUMBNAIL}`;
}

export {
    coverUri,
}