const queryBy = async (page, limit = 5) => {
  const ok = await new Promise((resolve) => {
    setTimeout(() => {
      resolve(true);
    }, 500);
  });

  const items = [];
  for (let i = limit * (page - 1); i < limit * page; i++) {
    items.push({
      id: i,
      title: 'msg -' + i,
      desc: 'msg from system',
      text: 'text text text',
      updated: '1 ngay truoc',
    });
  }
  const totalPage = 10;
  const total = totalPage * limit;
  return {
    page,
    limit,
    total,
    totalPage,
    items,
  };
};

export { queryBy };
