
import * as React from 'react';
import { View, Image, StyleSheet, FlatList } from 'react-native';
import {
    Subheading,
    Headline,
    Title,
    TouchableRipple,
    Button,
    Caption,
} from 'react-native-paper';
import Icon from 'react-native-vector-icons/Ionicons';

import { coverUri } from '../../utils/uri';

function hasMeta(view) {
    if(view === 'gridlarge') {
        return true;
    }
    return false;
}

function screenOf(screen) {
    if(!screen) return screen;
    if(screen === 'explore') return 'Explorer'
    if(screen === 'channel') return 'Channel'
    if(screen === 'rank')    return '#DHChart'
    return null;
}

function numColumsOf(view) {
    if(view === 'gridlarge') return 2
    if(view === 'gridsmall') return 3
    return 1;
}

function storiesOf(stories, view) {
    if(view === 'gridlarge') return stories.slice(0, 4)
    if(view === 'gridsmall') {
        if (stories.length >= 6) {
            return stories.slice(0, 6)
        }
        return stories.slice(0, 3)
    }
    return stories;
}

function itemStyleOf(index, view) {
    if(view === 'gridlarge') return {
        marginLeft: (index > 0 && index % 2 !== 0) ? 10: 0,
    }
    if(view === 'gridsmall') return {
        marginLeft: (index > 0 && index % 3 !== 0) ? 10: 0,
    }
    return {
        marginLeft: index > 0 ? 10: 0,
    }
}

function waperStyleOf(stdStories, view) {
    if(view === 'gridsmall') {
        if (stdStories.length >= 6) {
            return {minHeight: 500}
        }
        return {minHeight: 245}
    }
    if(view === 'gridlarge') return {
        minHeight: 500, 
    }
    if(view === 'hzlarge') return {
        minHeight: 310, 
    }
    if(view === 'hzsmall') return {
        minHeight: 200, 
    }
    return {}
}

const Item = ({ navigation, view, style, id, code, title, cover, author, external_cover_url }) => {    
    return (
        <TouchableRipple key={id} style={[style, styles[view].item]} onPress={()=> navigation.push('Details', {
            storyCode: code,
        }) }>
            <>
            <Image source={{uri: coverUri(cover, external_cover_url)}} 
                   style={styles[view].image} resizeMode={'cover'} />
            <Subheading>{title}</Subheading>
            {hasMeta(view) && <Caption style={styles[view].authorName}>{author.name}</Caption>}
            </>
        </TouchableRipple>
    )
};

const Collection = React.memo(({ navigation, theme, t, id, code, name, icon, view, screen, stories }) => {
    const stdStories = storiesOf(stories, view);
    const moreLink = screenOf(screen);

    return (<View style={[waperStyleOf(stdStories, view), styles[view].container]} onPress={()=> {}}>
        <View style={styles[view].headline}>
            <View style={styles[view].leftTitle}>
                <Icon size={24} color={theme.colors.accent} name={icon}/>
                <Subheading style={styles[view].titleText}>{name}</Subheading>
            </View>
            {moreLink && 
                <TouchableRipple style={styles[view].rightTitle} 
                                 onPress={()=> navigation.navigate(moreLink, {code})} >
                    <>
                        <Caption labelStyle={styles[view].moreLabel}>{t('More')}</Caption>
                        <Icon size={10} name={'chevron-forward'}/>
                    </>
                </TouchableRipple>
            }
        </View>
        <FlatList
            style={styles[view].list}
            horizontal={view.startsWith('hz')}
            scrollEnabled={view.startsWith('hz')}
            numColumns={numColumsOf(view)}
            data={stdStories}
            ItemSeparatorComponent={() => <View style={{height: 30}} />}    
            keyExtractor={(item) => item.id}
            renderItem={({ index, item }) => <Item 
                navigation={navigation} 
                view={view} {...item}
                style={itemStyleOf(index, view)}
            />}
        />
    </View>
    )}
);

const baseStyles = StyleSheet.create({
    container: {
        flex: 1,
        flexDirection: 'column',
    },
    headline: {
        // flex: 1,
        flexDirection: 'row',
        justifyContent: 'space-between',
        height: 35,
    },
    titleText: {
        textAlign: 'left',
    },
    leftTitle: {
        flex: 10,
        flexDirection: 'row',
        alignItems: 'center',
        columnGap: 5,
    },
    rightTitle: {
        flex: 2,
        flexDirection: 'row',
        columnGap: 1,
        alignItems: 'center',
    },
    moreLabel: {
        fontSize: 8,
    },
    list: {
        flex: 1,
        gap: 5,
    },
    item: {
        flex: 1,
        width: "49.5%",
    },
    image: {
        height: 135,
        borderRadius: 5,
    },
    storyTitle: {},
    authorName: {},

});
const hzSmallStyles = StyleSheet.create({
    ...baseStyles,
    item: {
        ...baseStyles.item,
        width: 164,
    },
    image: {
        ...baseStyles.image,
        height: 100,
    },
});
const hzLargeStyles = StyleSheet.create({
    ...hzSmallStyles,
    item: {
        ...hzSmallStyles.item,
        width: 164,
    },
    image: {
        ...hzSmallStyles.image,
        height: 216,
    },
});
const gridSmallStyles = StyleSheet.create({
    ...baseStyles,
    list: {
    },
    item: {
        ...baseStyles.item,
        width: "33%",
    },
    image: {
        ...baseStyles.image,
        height: 150,
    },
});
const gridLargeStyles = StyleSheet.create({
    ...gridSmallStyles,
    list: {
        ...gridSmallStyles.list,
    },
    item: {
        ...gridSmallStyles.item,
        width: '49.5%',
    },
    image: {
        ...gridSmallStyles.image,
        height: 135,
    }
});

const styles = {
    hzlarge: hzLargeStyles,
    hzsmall: hzSmallStyles,
    gridlarge: gridLargeStyles,
    gridsmall: gridSmallStyles
};

export default Collection;