import { gql } from "@apollo/client";

const GET_TOP_COLLECTIONS = gql`
    query getTopCollections($segments: [String!], $start: Int, $limit: Int) {
        collections(
            sort: "order", 
            filters: { segment: { in: $segments}},
            pagination: { start: $start, limit: $limit } ) {
        meta {
            pagination {
                total
                page
                pageSize
                pageCount
            }
        }
        data {
            id
            attributes {
            screen
            code
            name
            view
            icon
            stories {
                data {
                id
                attributes {
                    code
                    title
                    external_cover_url
                    cover {
                    data {
                        id
                        attributes {
                        url
                        }
                    }
                    }
                    author {
                        data {
                          id
                          attributes {
                            code
                            name
                          }
                        }
                      }
                    updatedAt
                }
                }
            }
            }
        }
        }
    }
  `
export {
    GET_TOP_COLLECTIONS
}