import * as React from 'react';
import {
    View,
    StyleSheet,
    FlatList,
    RefreshControl,
} from 'react-native';
import { useTranslation } from 'react-i18next';
import { Text, Subheading, useTheme } from 'react-native-paper';
import { useQuery, NetworkStatus } from "@apollo/client";

import StoryItem from './StoryItem';

import { Error } from '../error';
import { ListLoadmoreIndicator, ScreenLoadingIndicator } from '../indicators';
import { ListHeader, ItemSeparator, ListEmpty } from '../list';

import flatten from '../../utils/flatten';
import { GET_STORIES } from '../../api/graphql/channel';

const ListStories = ({ route, navigation }) => {
    const theme = useTheme();
    const { t } = useTranslation();
    const { loading, error, data, fetchMore, refetch, networkStatus } = useQuery(GET_STORIES, {
        notifyOnNetworkStatusChange: true,
        variables: {
            start: 0,
            limit: 2,
            tag: route.name,
        },
    });


    if (error) return <Error
        msg={error.message}
        onRefresh={() => refetch()}
    />

    const execFetchMore = () => {
        const { stories: { meta: { pagination } } } = data;
        if (pagination.page < pagination.pageCount) {
            fetchMore({
                variables: {
                    start: data.stories.data.length
                },
            });
        }
    }

    const renderFooter = (props) => {
        let noMore = false
        if (!loading && !error) {
            const { stories: { meta: { pagination: {
                page, pageCount
            } } } } = data;
            noMore = (page === pageCount)
        }
        return (
            <View style={styles.footer}>
                {networkStatus == NetworkStatus.fetchMore && <ListLoadmoreIndicator />}
                {noMore && <Subheading>{t('No more items at the moment')}</Subheading>}
            </View>
        )
    };

    return (
        <>
            {networkStatus === NetworkStatus.loading ? (
                <ScreenLoadingIndicator />
            ) : (
                <FlatList
                    contentContainerStyle={styles.list}
                    data={flatten(data.stories)}
                    ListHeaderComponent={ListHeader}
                    ListFooterComponent={renderFooter}
                    ListEmptyComponent={() => <ListEmpty onRefresh={() => refetch()} />}
                    ItemSeparatorComponent={null}
                    renderItem={({ item }) => (
                        <StoryItem key={item.id} {...item} navigation={navigation} />
                    )}
                    keyExtractor={(item) => item.id}
                    onEndReachedThreshold={0.2}
                    onEndReached={execFetchMore}
                    refreshControl={
                        <RefreshControl
                            refreshing={networkStatus === NetworkStatus.refetch}
                            onRefresh={() => refetch()}
                            title={t('loading stories..')}
                            titleColor={theme.colors.text}
                            colors={theme.colors.primary}
                            tintColor={theme.colors.primary}
                        />
                    }
                />
            )}
        </>
    )
};


const styles = StyleSheet.create({
    footer: {
        flex: 1,
        paddingBottom: 10,
        alignItems: 'center',
        justifyContent: 'center',
    },
    list: {
        flexGrow: 1,
        paddingHorizontal: 10,
    },
});
export default ListStories;

