import { gql } from "@apollo/client";

export const LIKE_STORY = gql`
    mutation likeStory($storyId: ID!) {
        likeStory(storyId: $storyId) {
            data {
                 id
            }
        }
    }
`

export const GET_STORY_LIKES = gql`
    query getStoryLikes($storyId: ID!) {
        story(id: $storyId) {
        data {
            id
            attributes {
                likes
                liked
            }
            }
        }
    }
`

export const LIKED_STORIES = gql`
    query likedStories($start: Int, $limit: Int) {
        likedStories(pagination: { start: $start, limit: $limit } ) {
        data {
            id
            attributes {
            story {
                data {
                id 
                attributes {
                    code
                    title
                    likes
                    createdAt
                }
                }
            }
            }
        }
        }
    }
`